import React, { useState } from 'react';
import './Contact.css';

const Contact: React.FC = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    company: '',
    phone: '',
    message: '',
    interest: ''
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Handle form submission
    console.log('Form submitted:', formData);
  };

  return (
    <section id="contact" className="section contact">
      <div className="container">
        <h2 className="section-title">Get in Touch</h2>
        <p className="section-subtitle">
          Ready to enhance your medical operations? Contact our team for personalized solutions.
        </p>

        <div className="contact-content">
          <div className="contact-info">
            <h3>Contact Information</h3>
            <div className="contact-methods">
              <div className="contact-method">
                <strong> Phone</strong>
                <span>+1 (555) 123-HELP</span>
              </div>
              <div className="contact-method">
                <strong> Email</strong>
                <span>solutions@medilabpro.com</span>
              </div>
              <div className="contact-method">
                <strong> Address</strong>
                <span>123 Medical Drive<br />Science Park, ST 12345</span>
              </div>
              <div className="contact-method">
                <strong> Support Hours</strong>
                <span>24/7 Technical Support<br />Business: Mon-Fri 8AM-6PM</span>
              </div>
            </div>

            <div className="emergency-contact">
              <h4>Emergency Technical Support</h4>
              <p>For urgent technical issues with medical equipment</p>
              <button className="btn btn-primary">Call Emergency Line</button>
            </div>
          </div>

          <form className="contact-form" onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="name">Full Name *</label>
              <input
                type="text"
                id="name"
                name="name"
                value={formData.name}
                onChange={handleChange}
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="email">Email Address *</label>
              <input
                type="email"
                id="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                required
              />
            </div>

            <div className="form-group">
              <label htmlFor="company">Organization</label>
              <input
                type="text"
                id="company"
                name="company"
                value={formData.company}
                onChange={handleChange}
              />
            </div>

            <div className="form-group">
              <label htmlFor="phone">Phone Number</label>
              <input
                type="tel"
                id="phone"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
              />
            </div>

            <div className="form-group">
              <label htmlFor="interest">Area of Interest</label>
              <select
                id="interest"
                name="interest"
                value={formData.interest}
                onChange={handleChange}
              >
                <option value="">Select an option</option>
                <option value="chemicals">Laboratory Chemicals</option>
                <option value="instruments">Medical Instruments</option>
                <option value="diagnostics">Diagnostic Kits</option>
                <option value="supplies">Sterile Supplies</option>
                <option value="custom">Custom Solutions</option>
              </select>
            </div>

            <div className="form-group">
              <label htmlFor="message">Message *</label>
              <textarea
                id="message"
                name="message"
                rows={4}
                value={formData.message}
                onChange={handleChange}
                required
              ></textarea>
            </div>

            <button type="submit" className="btn btn-primary">Send Message</button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default Contact;