import React, { useState } from "react";
import { ChevronLeft, ChevronRight, Heart } from "lucide-react";
import "./RecentlyViewed.css";
import { Product, ProductVariant } from "../../types"; //  use backend types

type Props = {
  products: Product[];
  onSelectProduct: (product: Product) => void;
  onToggleWishlist: (product: Product) => void;
  wishlist: Product[];
};

const DEFAULT_IMAGE = "https://via.placeholder.com/180";

export default function RecentlyViewed({
  products,
  onSelectProduct,
  onToggleWishlist,
  wishlist,
}: Props) {
  const itemsPerPage = 6;
  const [startIndex, setStartIndex] = useState(0);

  const handleNext = () => {
    setStartIndex((prev) =>
      prev + itemsPerPage < products.length ? prev + itemsPerPage : 0
    );
  };

  const handlePrev = () => {
    setStartIndex((prev) =>
      prev - itemsPerPage >= 0
        ? prev - itemsPerPage
        : Math.max(products.length - itemsPerPage, 0)
    );
  };

  const currentItems = products.slice(startIndex, startIndex + itemsPerPage);

  return (
    <div className="recently-viewed-container">
      <h2 className="recently-viewed-title">Recently Viewed Products</h2>

      {products.length === 0 ? (
        <div className="recently-empty-state">
          <div className="recently-empty-icon"></div>
          <p>No recently viewed products yet</p>
          <span>Start browsing to see your recently viewed items!</span>
        </div>
      ) : (
        <div className="slider-wrapper">
          <button className="side-btn" onClick={handlePrev} aria-label="Previous">
            <ChevronLeft size={20} />
          </button>

          <div className="cards-wrapper">
            {currentItems.map((item) => {
              const isWishlisted = wishlist.some((p) => p.id === item.id);

              //  Pick a variant (first or best available)
              const variant: ProductVariant | undefined = item.variants?.[0];
              const price = Number(variant?.price ?? variant?.est_price ?? 0);

              return (
                <div key={item.id} className="product-card">
                  <div
                    className="recent-product-image-container"
                    onClick={() => onSelectProduct(item)}
                  >
                    <img
                      src={item.image || DEFAULT_IMAGE}
                      alt={item.name}
                      className="recent-product-image"
                      onError={(e) => {
                        e.currentTarget.src = DEFAULT_IMAGE;
                      }}
                    />
                  </div>

                  <button
                    className={`heart-btn ${isWishlisted ? "wishlisted" : ""}`}
                    onClick={(e) => {
                      e.stopPropagation();
                      onToggleWishlist(item);
                    }}
                    aria-label={
                      isWishlisted ? "Remove from wishlist" : "Add to wishlist"
                    }
                  >
                    <Heart
                      size={20}
                      fill={isWishlisted ? "red" : "none"}
                      color={isWishlisted ? "red" : "currentColor"}
                    />
                  </button>

                  <div className="recent-product-info">
                    <div className="recent-product-name" title={item.name}>
                      {item.name}
                    </div>
                    {variant ? (
                      <div className="recent-product-price">
                        {price.toFixed(2)}{" "}
                        <span className="variant-label">
                          {variant.variant_label ? `(${variant.variant_label})` : ""}
                        </span>
                      </div>
                    ) : (
                      <div className="recent-product-price unavailable">
                        Price unavailable
                      </div>
                    )}
                  </div>
                </div>
              );
            })}
          </div>

          <button className="side-btn" onClick={handleNext} aria-label="Next">
            <ChevronRight size={20} />
          </button>
        </div>
      )}
    </div>
  );
}
