import React, { useState } from "react";
import "./QuotationPanel.css";
import { uploadQuotationFile } from "../../api/api";

type QuotationRequest = {
  id: number;
  product: {
    id: number;
    name: string;
    seller: string;
  };
  buyer: string;
  created_at: string;
  is_resolved: boolean;
};

interface QuotationPanelProps {
  token: string;
  onClose: () => void;
  requests: QuotationRequest[];
}

export default function QuotationPanel({ token, onClose, requests }: QuotationPanelProps) {
  const [loading, setLoading] = useState(false);
  const [uploadingRequestId, setUploadingRequestId] = useState<number | null>(null);

  const handleUpload = async (requestId: number, file: File) => {
    setLoading(true);
    setUploadingRequestId(requestId);
    try {
      await uploadQuotationFile(token, requestId, file);
      alert(" Quotation uploaded successfully!");
      // You might want to refresh the requests list here
    } catch (err: any) {
      console.error("Upload error:", err);
      alert(` Failed to upload quotation: ${err.message || "Unknown error"}`);
    } finally {
      setLoading(false);
      setUploadingRequestId(null);
    }
  };

  if (loading && !uploadingRequestId) {
    return (
      <div className="quotationpanel-panel">
        <div className="quotationpanel-loading">
          Loading quotation requests...
        </div>
      </div>
    );
  }

  if (!requests || requests.length === 0) {
    return (
      <div className="quotationpanel-panel">
        <div className="quotationpanel-empty-state">
          <div className="quotationpanel-empty-illustration"></div>
          <p>No quotation requests available at the moment.</p>
          <button className="quotationpanel-close-btn" onClick={onClose}>
            Close Panel
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="quotationpanel-panel">
      <div className="quotationpanel-header">
        <h2 className="quotationpanel-title">Quotation Requests</h2>
        <button className="quotationpanel-close-btn" onClick={onClose}>
           Close
        </button>
      </div>

      <div className="quotationpanel-requests-list">
        {requests.map((req) => (
          <div 
            key={req.id} 
            className={`quotationpanel-card ${req.is_resolved ? 'resolved' : 'pending'}`}
          >
            <div className="quotationpanel-status-badge ${req.is_resolved ? 'resolved' : 'pending'}">
              {req.is_resolved ? 'Resolved' : 'Pending'}
            </div>

            <div className="quotationpanel-card-info">
              <div className="quotationpanel-info-item">
                <span className="quotationpanel-info-label">Product</span>
                <span className="quotationpanel-info-value quotationpanel-product-name">
                  {req.product.name}
                </span>
              </div>

              <div className="quotationpanel-info-item">
                <span className="quotationpanel-info-label">Buyer Email</span>
                <span className="quotationpanel-info-value">{req.buyer}</span>
              </div>

              <div className="quotationpanel-info-item">
                <span className="quotationpanel-info-label">Seller Email</span>
                <span className="quotationpanel-info-value">{req.product.seller}</span>
              </div>

              <div className="quotationpanel-info-item">
                <span className="quotationpanel-info-label">Requested On</span>
                <span className="quotationpanel-info-value">
                  {new Date(req.created_at).toLocaleString()}
                </span>
              </div>
            </div>

            <div className="quotationpanel-file-upload">
              <input
                type="file"
                accept="application/pdf"
                onChange={(e) => {
                  if (e.target.files && e.target.files.length > 0) {
                    handleUpload(req.id, e.target.files[0]);
                    e.target.value = ""; // reset input
                  }
                }}
                className="quotationpanel-file-input"
                disabled={loading && uploadingRequestId === req.id}
              />
              {loading && uploadingRequestId === req.id && (
                <div className="quotationpanel-upload-progress">
                  <div className="quotationpanel-upload-progress-bar" style={{ width: '70%' }} />
                </div>
              )}
            </div>
          </div>
        ))}
      </div>

      <div className="quotationpanel-footer">
        <button className="quotationpanel-close-btn" onClick={onClose}>
          Close Panel
        </button>
      </div>
    </div>
  );
}