import React from "react";
import { CartItem, ProductVariant } from "../../types";
import "./CartPanel.css";

type Props = {
  products: CartItem[];
  onClose: () => void;
  onUpdateQuantity?: (
    cartItemId: number,
    productId: number,
    newQty: number,
    variantId?: number | null
  ) => void;
  onCheckout?: () => void;
  onClearItem?: (cartItemId: number) => void;
};


const DEFAULT_IMAGE = "https://via.placeholder.com/100";

export default function CartPanel({
  products,
  onClose,
  onUpdateQuantity,
  onCheckout,
  onClearItem,
}: Props) {
  //  Normalize and enrich products for display
  const safeProducts = products.map((p) => {
    const product = p.product;
    const image =
      product.image && product.image.startsWith("http")
        ? product.image
        : DEFAULT_IMAGE;

    //  use the variant actually stored in the CartItem, not the first variant
    const variant: ProductVariant | null = p.variant || null;

    const basePrice = Number(
    variant?.price ??
    variant?.est_price ??
    (product as any).price ??
    0
  );

  const discount = Number(product.discount || 0);
  const finalPrice = basePrice - (basePrice * discount) / 100;


    return {
      id: p.id,
      product,
      variant,
      image,
      basePrice,
      finalPrice,
      variantLabel: variant?.variant_label ?? "Default",
      quantity: p.quantity,
    };
  });

  const total = safeProducts.reduce(
    (sum, item) => sum + item.finalPrice * item.quantity,
    0
  );

  return (
    <div className="cart-overlay" onClick={onClose}>
      <div className="cart-panel" onClick={(e) => e.stopPropagation()}>
        <h2>Your Shopping Cart</h2>

        {safeProducts.length === 0 ? (
          <p className="cart-empty">Your cart is empty</p>
        ) : (
          <>
            <div className="cart-items">
              {safeProducts.map((item) => (
                <div className="cart-item" key={item.id}>
                  <img
                    src={item.image || DEFAULT_IMAGE}
                    alt={item.product.name}
                    className="cart-image"
                    onError={(e) =>
                      ((e.target as HTMLImageElement).src = DEFAULT_IMAGE)
                    }
                  />

                  <div className="cart-info">
                    <p className="cart-name">{item.product.name}</p>

                    {item.variantLabel && (
                      <p className="cart-variant">
                        Variant: <strong>{item.variantLabel}</strong>
                      </p>
                    )}

                    <p className="cart-price">
                      {item.product.discount ? (
                        <>
                          <span className="cart-old-price">
                            ${item.basePrice.toFixed(2)}
                          </span>
                          <span className="cart-discounted-price">
                            ${item.finalPrice.toFixed(2)}
                          </span>
                        </>
                      ) : (
                        <strong>${item.finalPrice.toFixed(2)}</strong>
                      )}{" "}
                       {item.quantity} ={" "}
                      <strong>${(item.finalPrice * item.quantity).toFixed(2)}</strong>
                    </p>

                    {onUpdateQuantity && (
                      <div className="cart-qty-row">
                        <button
                          className={`cart-qty-btn ${
                            item.quantity <= 1 ? "cart-qty-disabled" : ""
                          }`}
                          disabled={item.quantity <= 1}
                          onClick={() =>
                            onUpdateQuantity?.(
                              item.id,
                              item.product.id,
                              item.quantity - 1,
                              item.variant?.id ?? null //  pass variantId
                            )
                          }
                        >
                          -
                        </button>
                        <span className="cart-qty-text">{item.quantity}</span>
                        <button
                          className="cart-qty-btn"
                          onClick={() =>
                            onUpdateQuantity?.(
                              item.id,
                              item.product.id,
                              item.quantity + 1,
                              item.variant?.id ?? null //  pass variantId
                            )
                          }
                        >
                          +
                        </button>
                      </div>
                    )}
                    {onClearItem && ( //  full remove button
                      <button
                        className="cart-remove-btn"
                        onClick={() => onClearItem(item.id)}
                      >
                        Remove
                      </button>
                    )}
                    </div>
                </div>
              ))}
            </div>

            <p className="cart-total">
              Total:{" "}
              <span className="cart-total-amount">
                ${total.toFixed(2)}
              </span>
            </p>
          </>
        )}

        <div className="cart-actions">
          <button className="cart-close-btn" onClick={onClose}>
            Close
          </button>
          {onCheckout && safeProducts.length > 0 && (
            <button className="cart-checkout-btn" onClick={onCheckout}>
              Proceed to Checkout
            </button>
          )}
        </div>
      </div>
    </div>
  );
}
