import React, { useState, ChangeEvent, FormEvent } from "react";
import "./AddProduct.css";
import { addProduct } from "../../api/api";

interface AddProductProps {
  token: string;
  onClose: () => void;
  onAddSuccess?: () => void;
}

//  Updated variant type
type Variant = {
  variant_label: string;   // e.g. "1 Liter", "2 kg"
  est_price: number;       //  number only
  price: number | null;    //  number or null
};

export default function AddProduct({ token, onClose, onAddSuccess }: AddProductProps) {
  const [name, setName] = useState("");
  const [refNo, setRefNo] = useState("");
  const [description, setDescription] = useState("");
  const [category, setCategory] = useState("");
  const [hsn, setHsn] = useState("");
  const [discount, setDiscount] = useState("");
  const [brand, setBrand] = useState("");
  const [casNo, setCasNo] = useState("");
  const [image, setImage] = useState<File | null>(null);
  const [fileName, setFileName] = useState<string>("");
  const [variants, setVariants] = useState<Variant[]>([
    { variant_label: "", est_price: 0, price: null },
  ]);
  const [loading, setLoading] = useState(false);

  //  Handle image upload
  const handleImageChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
      setImage(file);

      let name = file.name.split("?")[0];
      const datetime = new Date().toISOString().replace(/[-:.]/g, "");
      name = `${datetime}_${name}`;

      if (name.length > 100) {
        const ext = name.split(".").pop() || "jpg";
        name = name.substring(name.length - 50) + "." + ext;
      }

      setFileName(name);
    }
  };

  //  Handle variant field change
  const handleVariantChange = (
    index: number,
    field: keyof Variant,
    value: string
  ) => {
    const updated = [...variants];

    if (field === "est_price") {
      updated[index][field] = parseFloat(value) || 0;
    } else if (field === "price") {
      updated[index][field] = value ? parseFloat(value) : null;
    } else {
      // for variant_label
      updated[index][field] = value as any;
    }

    setVariants(updated);
  };

  //  Add / remove variant rows
  const addVariantRow = () =>
    setVariants([...variants, { variant_label: "", est_price: 0, price: null }]);

  const removeVariantRow = (index: number) => {
    if (variants.length > 1) {
      setVariants(variants.filter((_, i) => i !== index));
    }
  };

  //  Handle form submission
  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();

    if (!name || !refNo || !category || !brand) {
      alert("Please fill all required fields (marked with *).");
      return;
    }

    const validVariants = variants.filter(
      (v) => v.variant_label && v.est_price > 0
    );

    if (validVariants.length === 0) {
      alert("Please add at least one variant with label and estimated price.");
      return;
    }

    setLoading(true);
    try {
      const formData = new FormData();
      formData.append("name", name);
      formData.append("ref_no", refNo);
      formData.append("description", description);
      formData.append("category", category);
      formData.append("hsn", hsn);
      formData.append("discount", discount);
      formData.append("brand", brand);
      formData.append("cas_no", casNo);

      if (image) formData.append("image", image, fileName);

      //  Convert variants to JSON string
      formData.append("variants", JSON.stringify(validVariants));

      await addProduct(token, formData);

      alert(" Product added successfully!");
      onClose();
      if (onAddSuccess) onAddSuccess();
    } catch (err: any) {
      console.error("Error adding product:", err);
      alert(" Failed to add product: " + (err.message || JSON.stringify(err)));
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="addproduct-add-container">
      <h2 className="addproduct-add-heading">Add New Product</h2>

      <form onSubmit={handleSubmit}>
        {/* --- Product Info --- */}
        <input
          className="addproduct-add-input"
          type="text"
          placeholder="Product Name *"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />

        <input
          className="addproduct-add-input"
          type="text"
          placeholder="Reference Number *"
          value={refNo}
          onChange={(e) => setRefNo(e.target.value)}
          required
        />

        <input
          className="addproduct-add-input"
          type="text"
          placeholder="Brand *"
          value={brand}
          onChange={(e) => setBrand(e.target.value)}
          required
        />

        <textarea
          className="addproduct-add-input addproduct-textarea"
          placeholder="Description"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />

        <select
          className="addproduct-add-input"
          value={category}
          onChange={(e) => setCategory(e.target.value)}
          required
        >
          <option value="">Select Category *</option>
          <option value="chemicals">Chemicals</option>
          <option value="instruments">Instruments</option>
          <option value="biologists">Biologists</option>
        </select>

        <input
          className="addproduct-add-input"
          type="text"
          placeholder="HSN Code"
          value={hsn}
          onChange={(e) => setHsn(e.target.value)}
        />

        <input
          className="addproduct-add-input"
          type="number"
          placeholder="Discount (%)"
          value={discount}
          onChange={(e) => setDiscount(e.target.value)}
          step="0.01"
          min="0"
        />

        <input
          className="addproduct-add-input"
          type="text"
          placeholder="CAS Number (e.g., 67-64-1)"
          value={casNo}
          onChange={(e) => setCasNo(e.target.value)}
        />

        {/*  Variant Section */}
        <h3 className="addproduct-variants-title">Variants</h3>
        {variants.map((variant, index) => (
          <div key={index} className="addproduct-variant-row">
            <input
              type="text"
              className="addproduct-add-input variant-input"
              placeholder="Variant (e.g. 1 Liter, 2 kg, Box of 10) *"
              value={variant.variant_label}
              onChange={(e) =>
                handleVariantChange(index, "variant_label", e.target.value)
              }
              required
            />
            <input
              type="number"
              className="addproduct-add-input variant-input"
              placeholder="Estimated Price *"
              value={variant.est_price}
              onChange={(e) =>
                handleVariantChange(index, "est_price", e.target.value)
              }
              step="0.01"
              min="0"
              required
            />
            <input
              type="number"
              className="addproduct-add-input variant-input"
              placeholder="Final Price"
              value={variant.price ?? ""}
              onChange={(e) =>
                handleVariantChange(index, "price", e.target.value)
              }
              step="0.01"
              min="0"
            />
            {variants.length > 1 && (
              <button
                type="button"
                className="addproduct-remove-btn"
                onClick={() => removeVariantRow(index)}
              >
                
              </button>
            )}
          </div>
        ))}
        <button
          type="button"
          className="addproduct-addvariant-btn"
          onClick={addVariantRow}
        >
           Add Variant
        </button>

        {/* Image Upload */}
        <div className="addproduct-image-row">
          <label className="addproduct-image-label">Product Image:</label>
          <input type="file" accept="image/*" onChange={handleImageChange} />
        </div>

        {fileName && <p className="addproduct-file-name">Selected: {fileName}</p>}

        {/* Submit Button */}
        <button
          type="submit"
          className="addproduct-submit-btn"
          disabled={loading}
        >
          {loading ? "Adding..." : "Add Product"}
        </button>
      </form>

      <button onClick={onClose} className="addproduct-cancel-btn">
        Cancel
      </button>
    </div>
  );
}
