// src/api/api.ts

// ---------- BASE ----------
const API_BASE =
  process.env.NODE_ENV === "production"
    ? "" // same domain when deployed (Render, Vercel, etc.)
    : "http://127.0.0.1:8000"; // local Django backend

// ---------- HELPERS ----------
async function handleResponse(res: Response) {
  if (!res.ok) {
    const errorData = await res.json().catch(() => ({}));
    throw new Error(errorData.detail || res.statusText || "API error");
  }
  return res.json();
}

async function authFetch(url: string, token: string|null, options: RequestInit = {}) {
  const res = await fetch(`${API_BASE}${url}`, {
    ...options,
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
      ...(options.headers || {}),
    },
  });
  return handleResponse(res);
}

// ===========================================================
// AUTHENTICATION & REGISTRATION
// ===========================================================
export async function registerUser(form: {
  name: string;
  phone: string;
  email: string;
  role: string;
  password: string;
  confirm_password: string;
}) {
  const res = await fetch(`${API_BASE}/api/register/`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(form),
  });
  return handleResponse(res);
}

export async function verifyOTPRegister(email: string, otp: string) {
  const res = await fetch(`${API_BASE}/api/verify-otp/`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ email, otp }),
  });
  return handleResponse(res);
}

export async function loginUser(email: string, password: string) {
  const res = await fetch(`${API_BASE}/api/token/`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ email, password }),
  });
  return handleResponse(res);
}

// ===========================================================
// PROFILE
// ===========================================================
export async function getProfile(token: string) {
  return authFetch("/api/profile/", token);
}

// ===========================================================
// PRODUCTS
// ===========================================================
export async function getProducts(token?: string) {
  const res = await fetch(`${API_BASE}/api/products/`, {
    headers: token ? { Authorization: `Bearer ${token}` } : {},
  });
  return handleResponse(res);
}

export async function addProduct(token: string, formData: FormData) {
  const res = await fetch(`${API_BASE}/api/products/add/`, {
    method: "POST",
    headers: { Authorization: `Bearer ${token}` },
    body: formData,
  });
  return handleResponse(res);
}


export async function uploadProductsExcel(token: string, file: File) {
  const formData = new FormData();
  formData.append("file", file);

  const res = await fetch(`${API_BASE}/api/products/upload_products/`, {
    method: "POST",
    headers: { Authorization: `Bearer ${token}` },
    body: formData,
  });

  if (!res.ok) {
    const text = await res.text();
    throw new Error(text || "Failed to upload products");
  }

  return res.json();
}

export async function updateProduct(token: string, productId: string, formData: FormData) {
  const res = await fetch(`${API_BASE}/api/products/${productId}/update/`, {
    method: "PUT",
    headers: { Authorization: `Bearer ${token}` },
    body: formData,
  });
  return handleResponse(res);
}

export async function deleteProduct(token: string, productId: string) {
  const res = await fetch(`${API_BASE}/api/products/${productId}/delete/`, {
    method: "DELETE",
    headers: { Authorization: `Bearer ${token}` },
  });
  if (!res.ok) {
    const text = await res.text();
    throw new Error(`Failed to delete product: ${text}`);
  }
  return { success: true };
}

// ===========================================================
// CART
// ===========================================================
export async function getCart(token: string) {
  return authFetch("/api/cart/", token);
}

export async function addToCart(
  token: string,
  productId: number | string,
  quantity: number,
  variantId?: number | null
) {
  const body: Record<string, any> = {
    product_id: productId,
    quantity,
  };
  if (variantId) body.variant_id = variantId;
  console.log(body)

  return authFetch("/api/cart/add/", token, {
    method: "POST",
    body: JSON.stringify(body),
  });
}


export async function removeCartItem(token: string, cartItemId: number) {
  return authFetch(`/api/cart/remove/${cartItemId}/`, token, { method: "DELETE" });
}

export async function clearCartItem(token: string, cartItemId: number) {
  return authFetch(`/api/cart/clear/${cartItemId}/`, token, { method: "DELETE" });
}

// ===========================================================
// WISHLIST
// ===========================================================
export async function getWishlist(token: string) {
  return authFetch("/api/wishlist/", token);
}

export async function toggleWishlist(token: string, productId: string, isInWishlist?: boolean) {
  const endpoint = isInWishlist
    ? `/api/wishlist/remove/${productId}/`
    : `/api/wishlist/add/`;
  const method = isInWishlist ? "DELETE" : "POST";
  const body = !isInWishlist ? JSON.stringify({ product_id: productId }) : undefined;

  await authFetch(endpoint, token, { method, body });
  return getWishlist(token);
}

