// ===========================================================
// CHAT & CONVERSATIONS
// ===========================================================
export async function getConversations(token: string) {
  return authFetch("/api/conversations/", token);
}

export async function getMessages(token: string, conversationId: number) {
  return authFetch(`/api/conversations/${conversationId}/messages/`, token);
}

export async function sendMessage(token: string, conversationId: number, text: string) {
  const formData = new FormData();
  formData.append("text", text);

  const res = await fetch(`${API_BASE}/api/conversations/${conversationId}/messages/send/`, {
    method: "POST",
    headers: { Authorization: `Bearer ${token}` },
    body: formData,
  });
  return handleResponse(res);
}

export async function startOrGetConversation(token: string, productId: string, orderId: number) {
  const formData = new FormData();
  formData.append("product_id", productId);
  formData.append("order_id", String(orderId));

  const res = await fetch(`${API_BASE}/api/conversations/start-or-get/`, {
    method: "POST",
    headers: { Authorization: `Bearer ${token}` },
    body: formData,
  });
  return handleResponse(res);
}

// ===========================================================
// QUOTATIONS
// ===========================================================
export async function getQuotationRequests(token: string) {
  return authFetch("/api/quotation-requests/", token);
}

export async function getQuotationForRequest(token: string, requestId: number) {
  return authFetch(`/api/quotation-requests/${requestId}/quotation/`, token);
}

export async function uploadQuotationFile(token: string, requestId: number, file: File) {
  const formData = new FormData();
  formData.append("file", file);
  formData.append("request", requestId.toString());

  const res = await fetch(`${API_BASE}/api/quotation-requests/${requestId}/upload/`, {
    method: "POST",
    headers: { Authorization: `Bearer ${token}` },
    body: formData,
  });

  if (!res.ok) {
    const text = await res.text();
    throw new Error(text || "Failed to upload quotation");
  }
  return res.json();
}

