// ===========================================================
// SEARCH
// ===========================================================
export async function searchProducts(query: string) {
  if (!query.trim()) return [];
  const res = await fetch(`${API_BASE}/api/search/?q=${encodeURIComponent(query)}`);
  return handleResponse(res);
}

