import React, { useEffect, useState, useRef } from "react";
import "./ChatPanel.css";
import {
  getConversations,
  getMessages,
  sendMessage,
} from "../../api/api";

type ChatPanelProps = {
  token: string;
  userEmail: string;
  conversationId?: number | null;
  onClose: () => void;
};

type Conversation = {
  id: number;
  order: number | null;
  product: { name: string } | null;
  buyer: string;
  seller: string;
  last_message?: string;
  unread_count?: number;
};

type Message = {
  id: number;
  sender: string;
  text: string;
  created_at: string;
  is_read: boolean;
};

export default function ChatPanel({
  token,
  userEmail,
  conversationId,
  onClose,
}: ChatPanelProps) {
  const [conversations, setConversations] = useState<Conversation[]>([]);
  const [messages, setMessages] = useState<Message[]>([]);
  const [newMessage, setNewMessage] = useState("");
  const [selectedConversation, setSelectedConversation] = useState<number | null>(
    conversationId ?? null
  );
  const [loading, setLoading] = useState(true);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // --- Load all conversations ---
  useEffect(() => {
    (async () => {
      try {
        setLoading(true);
        const data = await getConversations(token);
        setConversations(data);
      } catch (err) {
        console.error("Failed to load conversations:", err);
      } finally {
        setLoading(false);
      }
    })();
  }, [token]);

  // --- Load messages when conversation selected ---
  useEffect(() => {
    if (conversationId) loadMessages(conversationId);
  }, [conversationId]);

  // --- Scroll to bottom whenever messages change ---
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const loadMessages = async (convId: number) => {
    try {
      const data = await getMessages(token, convId);
      setMessages(data);
      setSelectedConversation(convId);
    } catch (err) {
      console.error("Error loading messages:", err);
    }
  };

  const handleSend = async () => {
    if (!newMessage.trim() || !selectedConversation) return;
    try {
      const msg = await sendMessage(token, selectedConversation, newMessage);
      setMessages((prev) => [...prev, msg]);
      setNewMessage("");
      // Update conversation preview
      setConversations((prev) =>
        prev.map((conv) =>
          conv.id === selectedConversation
            ? { ...conv, last_message: msg.text }
            : conv
        )
      );
    } catch (err) {
      console.error("Message send error:", err);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  const formatTime = (timestamp: string) =>
    new Date(timestamp).toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
    });

  const getInitials = (name: string) =>
    name
      .split(" ")
      .map((n) => n[0])
      .join("")
      .toUpperCase();

  return (
    <div className="chat-overlay" onClick={onClose}>
      <div className="chat-panel" onClick={(e) => e.stopPropagation()}>
        {/* Header */}
        <div className="chat-header">
          <div className="chat-header-content">
            <h2>Messages</h2>
            <p>Chat with sellers or buyers</p>
          </div>
          <button className="chat-close-btn" onClick={onClose}>
            
          </button>
        </div>

        <div className="chat-body">
          {/* Conversations List */}
          <div className="chat-conversations">
            <div className="chat-conversations-header">
              <h3>Conversations</h3>
              <span className="chat-conversations-count">
                {conversations.length}
              </span>
            </div>

            {loading ? (
              <div className="chat-loading">
                <div className="chat-spinner"></div>
                <span>Loading...</span>
              </div>
            ) : conversations.length === 0 ? (
              <div className="chat-empty-conversations">
                <div className="chat-empty-icon"></div>
                <p>No conversations yet</p>
                <span>Start chatting from your orders</span>
              </div>
            ) : (
              <div className="chat-conversations-list">
                {conversations.map((conv) => {
                  const partner =
                    conv.seller === userEmail ? conv.buyer : conv.seller;
                  const lastMessage = conv.last_message || "No messages yet";

                  return (
                    <div
                      key={conv.id}
                      className={`chat-conversation-item ${
                        selectedConversation === conv.id
                          ? "chat-conversation-active"
                          : ""
                      }`}
                      onClick={() => loadMessages(conv.id)}
                    >
                      <div className="chat-conversation-avatar">
                        {getInitials(partner || "U")}
                      </div>
                      <div className="chat-conversation-content">
                        <div className="chat-conversation-header">
                          <span className="chat-conversation-seller">
                            {partner || "User"}
                          </span>
                        </div>
                        <div className="chat-conversation-product">
                          {conv.product?.name ?? "Product"}
                        </div>
                        <div className="chat-conversation-preview">
                          {lastMessage}
                        </div>
                        {conv.unread_count && conv.unread_count > 0 && (
                          <span className="chat-conversation-unread">
                            {conv.unread_count}
                          </span>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>
            )}
          </div>

          {/* Messages Area */}
          {selectedConversation ? (
            <div className="chat-messages-area">
              {/* Header for current conversation */}
              {(() => {
                const current = conversations.find(
                  (c) => c.id === selectedConversation
                );
                if (!current) return null;
                const partner =
                  current.seller === userEmail ? current.buyer : current.seller;

                return (
                  <div className="chat-messages-header">
                    <div className="chat-current-conversation-avatar">
                      {getInitials(partner || "U")}
                    </div>
                    <div className="chat-current-conversation-info">
                      <h4>{partner}</h4>
                      <span>{current.product?.name ?? "Product"}</span>
                    </div>
                    <div className="chat-messages-actions">
                      <button className="chat-call-btn"></button>
                      <button className="chat-info-btn"></button>
                    </div>
                  </div>
                );
              })()}

              {/* Messages list */}
              <div className="chat-messages-container">
                {messages.length === 0 ? (
                  <div className="chat-empty-messages">
                    <div className="chat-empty-messages-icon"></div>
                    <p>No messages yet</p>
                    <span>Start the conversation</span>
                  </div>
                ) : (
                  <div className="chat-messages-list">
                    {messages.map((msg) => (
                      <div
                        key={msg.id}
                        className={`chat-message ${
                          msg.sender === userEmail
                            ? "chat-message-sent"
                            : "chat-message-received"
                        }`}
                      >
                        <div className="chat-message-bubble">
                          <p>{msg.text}</p>
                          <span className="chat-message-time">
                            {formatTime(msg.created_at)}
                          </span>
                        </div>
                      </div>
                    ))}
                    <div ref={messagesEndRef} />
                  </div>
                )}
              </div>

              {/* Input */}
              <div className="chat-input-container">
                <div className="chat-input-wrapper">
                  <input
                    type="text"
                    placeholder="Type a message..."
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    onKeyPress={handleKeyPress}
                    className="chat-input"
                  />
                  <button
                    onClick={handleSend}
                    disabled={!newMessage.trim()}
                    className="chat-send-btn"
                  >
                    <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                      <path d="M2.01 21L23 12 2.01 3 2 10l15 2-15 2z" />
                    </svg>
                  </button>
                </div>
              </div>
            </div>
          ) : (
            <div className="chat-no-conversation">
              <div className="chat-no-conversation-icon"></div>
              <h3>Select a conversation</h3>
              <p>Choose a chat from the list to start messaging</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
