// Sidebar.tsx
import React from "react";
import { useNavigate } from "react-router-dom";
import "./Sidebar.css";

type Props = {
  onWishlistPress: () => void;
  onOrdersPress: () => void;
  onChatPress: () => void;
  onQuotationsPress: () => void;

};

export default function Sidebar({
  onWishlistPress,
  onOrdersPress,
  onChatPress,
  onQuotationsPress,

}: Props) {
  const navigate = useNavigate();

  const handleLogout = () => {
    try {
      // Clear tokens (same as AsyncStorage.clear in RN)
      localStorage.removeItem("access");
      localStorage.removeItem("refresh");
      localStorage.removeItem("token");

      // Navigate to login page
      navigate("/login");
    } catch (err) {
      alert("Something went wrong while logging out.");
      console.error(err);
    }
  };

  return (
    <div className="sidebar">
      <div className="menu-items">
        <button className="sidebar-item" onClick={onOrdersPress}>
          Home
        </button>
        <button className="sidebar-item" onClick={onOrdersPress}>
          Orders
        </button>
        <button className="sidebar-item" onClick={onWishlistPress}>
          Wishlist
        </button>
        <button className="sidebar-item" onClick={onChatPress}>
          Chats
        </button>
        <button className="sidebar-item" onClick={onQuotationsPress}>
          Quotations
        </button>
        <button className="sidebar-item logout" onClick={handleLogout}>
          Logout
        </button>
      
      </div>

      
    </div>
  );
}
