import React, { useEffect, useState } from "react";
import "./OrdersList.css";
import { getOrders, startOrGetConversation } from "../../api/api";
import { Order, OrderItem } from "../../types";

type Props = {
  token: string;
  onClose: () => void;
  onStartChat: (conversationId: number) => void;
};

const DEFAULT_IMAGE = "https://via.placeholder.com/100";
const BACKEND_URL =
  process.env.NODE_ENV === "production" ? "" : "http://127.0.0.1:8000";

export default function OrdersList({ token, onClose, onStartChat }: Props) {
  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadOrders = async () => {
      try {
        const data = await getOrders(token);
        setOrders(data);
      } catch (err) {
        console.error("Error fetching orders:", err);
        alert("Could not load orders.");
        onClose();
      } finally {
        setLoading(false);
      }
    };
    loadOrders();
  }, [token, onClose]);

  // -----------------------------
  // Helpers
  // -----------------------------
  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case "completed":
        return "#10b981";
      case "pending":
        return "#f59e0b";
      case "cancelled":
        return "#ef4444";
      case "shipped":
        return "#3b82f6";
      case "processing":
        return "#8b5cf6";
      case "delivered":
        return "#06b6d4";
      default:
        return "#6b7280";
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status.toLowerCase()) {
      case "completed":
        return "";
      case "pending":
        return "";
      case "cancelled":
        return "";
      case "shipped":
        return "";
      case "processing":
        return "";
      case "delivered":
        return "";
      default:
        return "";
    }
  };

  const handleChatWithSeller = async (productId: string, orderId: number) => {
    try {
      const data = await startOrGetConversation(token, productId, orderId);
      onStartChat(data.id);
    } catch (err) {
      console.error("Chat start error:", err);
      alert("Could not start chat with seller.");
    }
  };

  const getEffectivePrice = (item: OrderItem): number => {
    const price =
      Number(item.price) ||
      Number(item.variant?.price) ||
      Number(item.variant?.est_price) ||
      0;
    return price;
  };

  // -----------------------------
  // Loading
  // -----------------------------
  if (loading)
    return (
      <div className="orderlist-overlay">
        <div className="orderlist-panel">
          <div className="orderlist-loader">
            <div className="orderlist-spinner"></div>
            Loading your orders...
          </div>
        </div>
      </div>
    );

  // -----------------------------
  // Main Render
  // -----------------------------
  return (
    <div className="orderlist-overlay" onClick={onClose}>
      <div className="orderlist-panel" onClick={(e) => e.stopPropagation()}>
        {/* ---------- Header ---------- */}
        <div className="orderlist-header">
          <div className="orderlist-header-content">
            <h2>My Orders</h2>
            <p>Track, manage, and chat with sellers</p>
          </div>
          <button className="orderlist-close-header-btn" onClick={onClose}>
            
          </button>
        </div>

        {/* ---------- Summary ---------- */}
        <div className="orderlist-summary">
          <div className="orderlist-summary-item">
            <span className="orderlist-summary-label">Total Orders</span>
            <span className="orderlist-summary-value">{orders.length}</span>
          </div>
          <div className="orderlist-summary-item">
            <span className="orderlist-summary-label">Active</span>
            <span className="orderlist-summary-value">
              {
                orders.filter(
                  (o) =>
                    !["completed", "cancelled"].includes(o.status.toLowerCase())
                ).length
              }
            </span>
          </div>
        </div>

        {/* ---------- Empty ---------- */}
        {orders.length === 0 ? (
          <div className="orderlist-empty">
            <div className="orderlist-empty-icon"></div>
            <h3>No orders yet</h3>
            <p>Your purchases will appear here once you order something.</p>
          </div>
        ) : (
          <div className="orderlist-content">
            {orders.map((order) => (
              <div key={order.id} className="orderlist-card">
                {/* ---------- Order Header ---------- */}
                <div className="orderlist-card-header">
                  <div className="orderlist-card-info">
                    <div className="orderlist-order-id">
                      Order #ORD-{order.id.toString().padStart(6, "0")}
                    </div>
                    <div
                      className="orderlist-status-badge"
                      style={{
                        backgroundColor: getStatusColor(order.status),
                      }}
                    >
                      {getStatusIcon(order.status)}{" "}
                      {order.status.charAt(0).toUpperCase() +
                        order.status.slice(1)}
                    </div>
                  </div>

                  <div className="orderlist-card-meta">
                    <div className="orderlist-total">
                      <span className="orderlist-total-label">Total</span>
                      <span className="orderlist-total-amount">
                        {Number(order.total_price).toFixed(2)}
                      </span>
                    </div>
                  </div>
                </div>

                {/* ---------- Date ---------- */}
                <div className="orderlist-date-info">
                  <span className="orderlist-date-icon"></span>
                  Ordered on{" "}
                  {new Date(order.created_at).toLocaleDateString("en-US", {
                    year: "numeric",
                    month: "long",
                    day: "numeric",
                    hour: "2-digit",
                    minute: "2-digit",
                  })}
                </div>

                {/* ---------- Items ---------- */}
                <div className="orderlist-products-section">
                  <div className="orderlist-products-header">
                    <span>Items ({order.items.length})</span>
                    {order.items.length > 2 && (
                      <span className="orderlist-scroll-hint"> Scroll </span>
                    )}
                  </div>

                  <div className="orderlist-products-scroll-container">
                    <div className="orderlist-products-scroll">
                      {order.items.map((item) => {
                        const price = getEffectivePrice(item);
                        const totalItemPrice = price * item.quantity;
                        const variantLabel =
                          item.variant?.variant_label || "";

                        return (
                          <div key={item.id} className="orderlist-product-card">
                            {/* ---------- Product Image ---------- */}
                            <div className="orderlist-product-image-container">
                              <img
                                src={
                                  item.product.image
                                    ? item.product.image.startsWith("http")
                                      ? item.product.image
                                      : `${BACKEND_URL}${item.product.image}`
                                    : DEFAULT_IMAGE
                                }
                                alt={item.product.name}
                                className="orderlist-product-image"
                                onError={(e) =>
                                  ((e.target as HTMLImageElement).src =
                                    DEFAULT_IMAGE)
                                }
                              />
                              <div className="orderlist-product-quantity">
                                {item.quantity}
                              </div>
                            </div>

                            {/* ---------- Product Info ---------- */}
                            <div className="orderlist-product-info">
                              <h4 className="orderlist-product-name">
                                <strong> {item.product.name}</strong>
                              </h4>

                              {/*  Show real variant label */}
                              {item.variant?.variant_label && (
                                <p className="orderlist-variant-label">
                                  {" "}
                                  <strong>{item.variant.variant_label}</strong>
                                </p>
                              )}

                              <div className="orderlist-product-meta">
                                <span className="orderlist-product-price">
                                  {price.toFixed(2)} each
                                </span>
                              </div>

                              <div className="orderlist-product-total">
                                Total: {totalItemPrice.toFixed(2)}
                              </div>

                              <div className="orderlist-product-seller">
                                 {item.product.seller || "Unknown Seller"}
                              </div>
                            </div>

                            {/* ---------- Chat ---------- */}
                            <button
                              className="orderlist-chat-btn"
                              onClick={() =>
                                handleChatWithSeller(
                                  item.product.id.toString(),
                                  order.id
                                )
                              }
                            >
                               Chat Seller
                            </button>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}

        {/* ---------- Footer ---------- */}
        <div className="orderlist-footer">
          <button className="orderlist-close-btn" onClick={onClose}>
            Close Orders
          </button>
        </div>
      </div>
    </div>
  );
}

