import React, { useState, useEffect } from "react";
import "./ProductDetails.css";
import {
  addToCart,
  getProductReviews,
  addProductReview,
  checkReviewEligibility,
} from "../../api/api";
import { Product, ProductVariant, Review } from "../../types";

type Props = {
  product: Product;
  token?: string;
  onBack: () => void;
  onAddToCart: (product: Product, quantity: number, variant?: ProductVariant) => void;
};

export default function ProductDetails({ product, onBack, onAddToCart, token }: Props) {
  const [selectedVariant, setSelectedVariant] = useState<ProductVariant | null>(
    product.variants && product.variants.length > 0 ? product.variants[0] : null
  );
  const [quantity, setQuantity] = useState(1);
  const [reviews, setReviews] = useState<Review[]>([]);
  const [eligibleToReview, setEligibleToReview] = useState(false);
  const [newRating, setNewRating] = useState(0);
  const [newComment, setNewComment] = useState("");
  const [loading, setLoading] = useState(false);

  // === Load reviews + eligibility ===
  useEffect(() => {
    (async () => {
      try {
        const data = await getProductReviews(product.id.toString());
        setReviews(data || []);
      } catch (err) {
        console.error("Failed to load reviews", err);
      }
    })();
    if (token) {
      (async () => {
        try {
          const res = await checkReviewEligibility(token, product.id.toString());
          setEligibleToReview(res.eligible);
        } catch {
          /* ignore */
        }
      })();
    }
  }, [product.id, token]);

  // === Variant Selection ===
  const handleVariantChange = (variant: ProductVariant) => setSelectedVariant(variant);

  // === Add to Cart ===
  const handleAddToCart = async () => {
    if (!token) return alert("Please log in to add to cart");
    try {
      setLoading(true);
      const variantId = selectedVariant ? selectedVariant.id : null;
     
      await addToCart(token, product.id, quantity, variantId);
      onAddToCart(product, quantity, selectedVariant || undefined);
      alert(`${quantity}  ${product.name} (${selectedVariant?.variant_label || "Default"}) added`);
    } catch (err) {
      console.error(err);
      alert("Failed to add to cart");
    } finally {
      setLoading(false);
    }
  };

  // === Review Submit ===
  const handleAddReview = async () => {
    if (!token) return alert("Please log in to review");
    if (newRating === 0) return alert("Select a rating");
    try {
      await addProductReview(token, product.id.toString(), newRating, newComment);
      alert("Review added!");
      setNewRating(0);
      setNewComment("");
      const updated = await getProductReviews(product.id.toString());
      setReviews(updated.reviews || []);
    } catch (err) {
      console.error("Review failed", err);
      alert("Could not submit review");
    }
  };

  const increment = () => setQuantity((q) => Math.min(q + 1, 99));
  const decrement = () => setQuantity((q) => Math.max(q - 1, 1));

  const displayedPrice =
  selectedVariant?.price && selectedVariant.price > 0
    ? selectedVariant.price
    : selectedVariant?.est_price && selectedVariant.est_price > 0
    ? selectedVariant.est_price
    : null;

  return (
    <div className="productpage-container">
      {/* ===== Top Bar ===== */}
      <div className="productpage-top-bar">
        <button className="productpage-back-btn" onClick={onBack}>
           Back to Products
        </button>
        <button className="productpage-quote-btn" onClick={() => alert("Quotation feature coming soon!")}>
          Request Quotation
        </button>
      </div>

      {/* ===== Product Content ===== */}
      <div className="productpage-content">
        {/* Image */}
        <div className="productpage-image-wrapper">
          <img src={product.image || "/placeholder.png"} alt={product.name} className="productpage-image" />
          {selectedVariant && (
            <span className="productpage-variant-badge">{selectedVariant.variant_label}</span>
          )}
        </div>

        {/* Info */}
        <div className="productpage-info">
          <h2 className="productpage-name">{product.name}</h2>

          {/* Ratings */}
          {product.average_rating ? (
            <div className="productpage-rating">
              {"".repeat(Math.round(product.average_rating))}
              {"".repeat(5 - Math.round(product.average_rating))}
              <span className="productpage-rating-text">
                {product.average_rating.toFixed(1)} / 5 ({product.total_reviews} reviews)
              </span>
            </div>
          ) : (
            <div className="productpage-no-rating">No ratings yet</div>
          )}

          {/* Meta Info */}
          <div className="productpage-meta">
            <p><strong>Brand:</strong> {product.brand}</p>
            <p><strong>Category:</strong> {product.category}</p>
            {product.cas_no && <p><strong>CAS No:</strong> {product.cas_no}</p>}
            {product.hsn && <p><strong>HSN Code:</strong> {product.hsn}</p>}
          </div>

          {/* Variants */}
          {product.variants && product.variants.length > 0 && (
            <div className="variant-selector">
              <label>Select Variant:</label>
              <div className="variant-buttons">
                {product.variants.map((v) => (
                  <button
                    key={v.id}
                    className={`variant-btn ${selectedVariant?.id === v.id ? "active" : ""}`}
                    onClick={() => handleVariantChange(v)}
                  >
                    {v.variant_label}
                  </button>
                ))}
              </div>
            </div>
          )}

          {/* Price */}
          <p className="productpage-price">
            {displayedPrice ? (
              <>
                ${displayedPrice}{" "}
                {selectedVariant?.price == null && (
                  <span className="est-price-tag">(est.)</span>
                )}
              </>
            ) : (
              <span className="productpage-none-price">None</span>
            )}
          </p>


          <p className="productpage-description">{product.description || "No description available."}</p>

          {/* Quantity */}
          <div className="productpage-quantity-section">
            <label>Quantity:</label>
            <div className="productpage-quantity-row">
              <button className="productpage-qty-btn" onClick={decrement}></button>
              <input
                type="number"
                min={1}
                max={99}
                value={quantity}
                onChange={(e) => setQuantity(Number(e.target.value))}
                className="productpage-qty-input"
              />
              <button className="productpage-qty-btn" onClick={increment}>+</button>
            </div>
          </div>

          <button
            className="productpage-add-to-cart-btn"
            onClick={handleAddToCart}
            disabled={loading}
          >
            {loading ? "Adding..." : "Add to Cart"}
          </button>
        </div>
      </div>

      {/* ===== Reviews ===== */}
      <div className="productpage-reviews-section">
        <h3 className="reviews-title">Customer Reviews</h3>
        {reviews.length === 0 ? (
          <p className="no-reviews">No reviews yet. Be the first!</p>
        ) : (
          reviews.map((r) => (
            <div key={r.id} className="review-card">
              <div className="review-header">
                <span className="review-stars">
                  {"".repeat(r.rating)}{"".repeat(5 - r.rating)}
                </span>
                <span className="review-buyer">{r.buyer}</span>
                <span className="review-date">
                  {new Date(r.created_at).toLocaleDateString()}
                </span>
              </div>
              <p className="review-comment">{r.comment || "No comment."}</p>
            </div>
          ))
        )}

        {token && eligibleToReview && (
          <div className="add-review-form">
            <h4>Add Your Review</h4>
            <div className="star-selector">
              {[1, 2, 3, 4, 5].map((s) => (
                <span
                  key={s}
                  className={`star ${s <= newRating ? "selected" : ""}`}
                  onClick={() => setNewRating(s)}
                >
                  
                </span>
              ))}
            </div>
            <textarea
              rows={3}
              value={newComment}
              onChange={(e) => setNewComment(e.target.value)}
              placeholder="Write a comment..."
              className="review-textarea"
            />
            <button className="submit-review-btn" onClick={handleAddReview}>
              Submit Review
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
