import React, { useEffect, useState } from "react";
import { getNotifications, markNotificationRead } from "../../api/api";
import "./NotificationsPanel.css";

// Notification type
export type Notification = {
  id: number;
  message: string;
  is_read: boolean;
  created_at: string;
};

// Props
export type NotificationsPanelProps = {
  token: string | null;
  onClose: () => void;
};

export default function NotificationsPanel({ token, onClose }: NotificationsPanelProps) {
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    //  Only load if token is present
    if (token) {
      loadNotifications();
    } else {
      setLoading(false); // no need to show spinner if not logged in
    }
  }, [token]);

  //  Fetch all notifications
  const loadNotifications = async () => {
    try {
      setLoading(true);
      const data = await getNotifications(token);
      setNotifications(data);
    } catch (err) {
      console.error("Error fetching notifications:", err);
    } finally {
      setLoading(false);
    }
  };

  //  Mark notification as read
  const handleMarkRead = async (id: number) => {
    if (!token) return; //  skip if not logged in
    try {
      await markNotificationRead(token, id);
      setNotifications((prev) =>
        prev.map((n) =>
          n.id === id ? { ...n, is_read: true } : n
        )
      );
    } catch (err) {
      console.error("Error marking notification as read:", err);
    }
  };

  return (
    <div className="notifications-overlay" onClick={onClose}>
      <div className="notifications-panel" onClick={(e) => e.stopPropagation()}>
        {/* Header */}
        <div className="notifications-header">
          <h2>Notifications</h2>
          <button className="close-btn" onClick={onClose}>
            
          </button>
        </div>

        {/* Not logged in */}
        {!token ? (
          <div className="notifications-empty">
            <div className="notifications-empty-icon"></div>
            <p>Please log in to view notifications.</p>
          </div>
        ) : loading ? (
          <div className="notifications-loading">
            <div className="spinner"></div>
            <p>Loading notifications...</p>
          </div>
        ) : notifications.length === 0 ? (
          <div className="notifications-empty">
            <div className="notifications-empty-icon"></div>
            <p>No notifications yet</p>
            <span>Well let you know when theres something new.</span>
          </div>
        ) : (
          <div className="notifications-list">
            {notifications.map((item) => (
              <div
                key={item.id}
                className={`notification-item ${item.is_read ? "read" : "unread"}`}
              >
                <div className="notification-content">
                  <p className="message">{item.message}</p>
                  <span className="date">
                    {new Date(item.created_at).toLocaleString()}
                  </span>
                </div>

                {!item.is_read && (
                  <button
                    className="mark-read-btn"
                    onClick={() => handleMarkRead(item.id)}
                  >
                    Mark Read
                  </button>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
