import React, { useState } from "react";
import "./UpdateProduct.css";
import { updateProduct, deleteProduct } from "../../api/api";

// -------------------- Types --------------------
type Variant = {
  id?: string;
  variant_label: string; // e.g. "1 Liter", "2 kg"
  est_price: number;
  price?: number | null;
  created_at?: string;
};

type Product = {
  id: string;
  name: string;
  ref_no: string;
  description?: string;
  category: string;
  image?: string | null;
  hsn?: string;
  discount?: number;
  brand: string;
  cas_no?: string;
  seller: string;
  created_at: string;
  updated_at: string;
  variants: Variant[];
  average_rating?: number | null;
  total_reviews?: number;
};

interface Props {
  product: Product;
  token: string;
  onBack: () => void;
  onUpdateSuccess?: (updated: Product) => void;
}

// -------------------- Component --------------------
export default function UpdateProduct({
  product,
  token,
  onBack,
  onUpdateSuccess,
}: Props) {
  const [name, setName] = useState(product.name);
  const [refNo, setRefNo] = useState(product.ref_no);
  const [description, setDescription] = useState(product.description || "");
  const [category, setCategory] = useState(product.category);
  const [hsn, setHsn] = useState(product.hsn || "");
  const [discount, setDiscount] = useState(String(product.discount || ""));
  const [brand, setBrand] = useState(product.brand);
  const [casNo, setCasNo] = useState(product.cas_no || "");
  const [image, setImage] = useState(product.image || "");
  const [file, setFile] = useState<File | null>(null);
  const [variants, setVariants] = useState<Variant[]>(
    product.variants?.length
      ? product.variants.map((v) => ({
          id: v.id,
          variant_label: v.variant_label || "",
          est_price: Number(v.est_price) || 0,
          price: v.price !== undefined && v.price !== null ? Number(v.price) : null,
        }))
      : [{ variant_label: "", est_price: 0, price: null }]
  );

  const [cacheBust, setCacheBust] = useState(Date.now());
  const [loading, setLoading] = useState(false);

  //  Handle image upload
  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
      setFile(file);
      const url = URL.createObjectURL(file);
      setImage(url);
    }
  };

  //  Handle variant field updates (with numeric conversion)
  const handleVariantChange = (
    index: number,
    field: keyof Variant,
    value: string
  ) => {
    const updated = [...variants];

    if (field === "est_price") {
      updated[index][field] = value === "" ? 0 : Number(value);
    } else if (field === "price") {
      updated[index][field] = value === "" ? null : Number(value);
    } else {
      // for variant_label
      updated[index][field] = value as any;
    }

    setVariants(updated);
  };

  //  Add / Remove variant rows
  const addVariantRow = () =>
    setVariants([...variants, { variant_label: "", est_price: 0, price: null }]);

  const removeVariantRow = (index: number) => {
    if (variants.length > 1) {
      setVariants(variants.filter((_, i) => i !== index));
    }
  };

  //  Delete product
  const handleDelete = async () => {
    if (!window.confirm(" Are you sure you want to delete this product?")) return;

    setLoading(true);
    try {
      await deleteProduct(token, product.id);
      alert(" Product deleted successfully!");
      onBack();
    } catch (err: any) {
      console.error("Delete error:", err);
      alert(" Failed to delete product: " + (err.message || JSON.stringify(err)));
    } finally {
      setLoading(false);
    }
  };

  //  Update product
  const handleUpdate = async () => {
    if (!name || !refNo || !category || !brand) {
      alert("Please fill all required fields (*)");
      return;
    }

    const validVariants = variants.filter(
      (v) => v.variant_label && v.est_price > 0
    );
    if (validVariants.length === 0) {
      alert("Please include at least one variant with label and estimated price.");
      return;
    }

    setLoading(true);
    try {
      const formData = new FormData();
      formData.append("name", name);
      formData.append("ref_no", refNo);
      formData.append("description", description);
      formData.append("category", category);
      if (hsn) formData.append("hsn", hsn);
      if (discount) formData.append("discount", discount);
      formData.append("brand", brand);
      if (casNo) formData.append("cas_no", casNo);
      if (file) formData.append("image", file);

      //  Include variant info as JSON
      formData.append("variants", JSON.stringify(validVariants));

      const updated = await updateProduct(token, product.id, formData);
      alert(" Product updated successfully!");
      setCacheBust(Date.now());
      if (onUpdateSuccess) onUpdateSuccess(updated);
      onBack();
    } catch (err: any) {
      console.error("Update error:", err);
      alert(" Failed to update product: " + (err.message || JSON.stringify(err)));
    } finally {
      setLoading(false);
    }
  };

  const categorySuggestions = ["chemicals", "instruments", "biologists"];

  // -------------------- JSX --------------------
  return (
    <div className="updateproduct-details-container">
      {/*  Header with Back + Delete */}
      <div className="updateproduct-header-row">
        <button className="updateproduct-back-button" onClick={onBack}>
           Back
        </button>
        <button
          className="updateproduct-delete-button"
          onClick={handleDelete}
          disabled={loading}
        >
           Delete
        </button>
      </div>

      {/*  Image section */}
      <div className="updateproduct-image-wrapper">
        <div className="updateproduct-image-preview">
          {image ? (
            <img
              src={`${image}?v=${cacheBust}`}
              alt={name}
              className="updateproduct-product-image"
            />
          ) : (
            <p>No image available</p>
          )}
        </div>
        <label className="updateproduct-change-image-label">
           Tap to Change Image
          <input type="file" accept="image/*" onChange={handleImageChange} />
        </label>
      </div>

      {/*  Product fields */}
      <input
        className="updateproduct-product-input"
        type="text"
        value={name}
        onChange={(e) => setName(e.target.value)}
        placeholder="Product Name *"
      />

      <input
        className="updateproduct-product-input"
        type="text"
        value={refNo}
        onChange={(e) => setRefNo(e.target.value)}
        placeholder="Reference Number *"
      />

      <input
        className="updateproduct-product-input"
        type="text"
        value={brand}
        onChange={(e) => setBrand(e.target.value)}
        placeholder="Brand *"
      />

      <textarea
        className="updateproduct-product-input updateproduct-textarea"
        value={description}
        onChange={(e) => setDescription(e.target.value)}
        placeholder="Description"
      />

      <input
        className="updateproduct-product-input"
        type="text"
        value={category}
        onChange={(e) => setCategory(e.target.value)}
        placeholder="Category *"
        list="category-suggestions"
      />
      <datalist id="category-suggestions">
        {categorySuggestions.map((cat) => (
          <option key={cat} value={cat} />
        ))}
      </datalist>

      <input
        className="updateproduct-product-input"
        type="text"
        value={hsn}
        onChange={(e) => setHsn(e.target.value)}
        placeholder="HSN Code"
      />

      <input
        className="updateproduct-product-input"
        type="number"
        value={discount}
        onChange={(e) => setDiscount(e.target.value)}
        placeholder="Discount (%)"
        step="0.01"
        min="0"
      />

      <input
        className="updateproduct-product-input"
        type="text"
        value={casNo}
        onChange={(e) => setCasNo(e.target.value)}
        placeholder="CAS Number (e.g., 67-64-1)"
      />

      {/*  Variants Section */}
      <h3 className="updateproduct-variants-title">Variants</h3>
      {variants.map((variant, index) => (
        <div key={index} className="updateproduct-variant-row">
          <input
            type="text"
            className="updateproduct-product-input variant-input"
            placeholder="Variant (e.g. 1 Liter, 2 kg, Box of 10) *"
            value={variant.variant_label}
            onChange={(e) =>
              handleVariantChange(index, "variant_label", e.target.value)
            }
          />
          <input
            type="number"
            className="updateproduct-product-input variant-input"
            placeholder="Estimated Price *"
            value={variant.est_price}
            onChange={(e) =>
              handleVariantChange(index, "est_price", e.target.value)
            }
            step="0.01"
            min="0"
          />
          <input
            type="number"
            className="updateproduct-product-input variant-input"
            placeholder="Final Price"
            value={variant.price ?? ""}
            onChange={(e) =>
              handleVariantChange(index, "price", e.target.value)
            }
            step="0.01"
            min="0"
          />
          {variants.length > 1 && (
            <button
              type="button"
              className="updateproduct-remove-btn"
              onClick={() => removeVariantRow(index)}
            >
              
            </button>
          )}
        </div>
      ))}
      <button
        type="button"
        className="updateproduct-addvariant-btn"
        onClick={addVariantRow}
      >
         Add Variant
      </button>

      {/*  Update button */}
      <button
        className="updateproduct-update-button"
        onClick={handleUpdate}
        disabled={loading}
      >
        {loading ? " Updating..." : "Update Product"}
      </button>
    </div>
  );
}
