import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import FormInput from "../components/FormInput";
import { loginUser, getProfile } from "../api/api";
import "./Login.css";

export default function Login() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [rememberMe, setRememberMe] = useState(false);

  const handleLogin = async () => {
    if (!email.trim() || !password.trim()) {
      alert("Please fill in all fields.");
      return;
    }

    setLoading(true);

    try {
        // Step 1: Request JWT token
      const tokenData = await loginUser(email, password);
      const token = tokenData.access;
      localStorage.setItem("token", token);

      const profileData = await getProfile(token);
      const role = profileData.role?.toLowerCase() || "";

      if (role === "buyer") {
        navigate("/buyer/home");
      } else if (role === "seller") {
        navigate("/seller/home");
      } else {
        alert("Invalid role in profile.");
      }
    } catch (err: any) {
      console.error(err);
      alert(err.message || "Invalid email or password. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !loading) {
      handleLogin();
    }
  };

  return (
    <div className="login-form-container">
      <h2>Welcome Back</h2>
      <p className="login-form-subtitle">Sign in to your account to continue</p>

      <div className="login-form-group">
        <FormInput
          placeholder="Email address"
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          
        />
      </div>

      <div className="login-form-group">
        <FormInput
          placeholder="Password"
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          
        />
      </div>

      <div className="login-remember-forgot">
        <label className="login-remember">
          <input
            type="checkbox"
            checked={rememberMe}
            onChange={(e) => setRememberMe(e.target.checked)}
          />
          Remember me
        </label>
        <span 
          className="login-forgot-password"
          onClick={() => navigate("/forgot-password")}
        >
          Forgot password?
        </span>
      </div>

      <button 
        className="login-submit-btn" 
        onClick={handleLogin} 
        disabled={loading}
        
      >
        {loading ? (
          <>
            <span className="login-loading-spinner"></span>
            Signing in...
          </>
        ) : (
          "Sign In"
        )}
      </button>

      <p className="login-register-link">
        Don't have an account?{" "}
        <span 
          className="login-link" 
          onClick={() => navigate("/register")}
          tabIndex={0}
          
        >
          Create account
        </span>
      </p>
    </div>
  );
}