import React, { useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { verifyOTPRegister } from "../api/api";
import "./VerifyOTP.css";

export default function VerifyOTP() {
  const location = useLocation();
  const navigate = useNavigate();
  const email = (location.state as { email?: string })?.email || "";

  const [otp, setOtp] = useState("");
  const [loading, setLoading] = useState(false);

  async function handleVerify(e: React.FormEvent) {
    e.preventDefault();
    if (!otp.trim()) {
      alert("Please enter OTP");
      return;
    }
    setLoading(true);
    try {
      const res = await verifyOTPRegister(email, otp);
      alert(res.message || " Registration successful! Please login.");
      navigate("/login");
    } catch (error: any) {
      alert(" Invalid OTP: " + (error.message || "Try again"));
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="otp-container">
      <h2>Email Verification</h2>
      <p className="otp-subtitle">
        Enter the 6-digit OTP sent to <strong>{email}</strong>
      </p>

      <form onSubmit={handleVerify}>
        <input
          type="text"
          maxLength={6}
          value={otp}
          onChange={(e) => setOtp(e.target.value)}
          placeholder="Enter OTP"
          className="otp-input"
        />
        <button type="submit" disabled={loading} className="otp-submit-btn">
          {loading ? "Verifying..." : "Verify OTP"}
        </button>
      </form>
    </div>
  );
}
