import React, { useState, ChangeEvent } from "react";
import FormInput from "../components/FormInput";
import RoleSelector from "../components/RoleSelector";
import { registerUser } from "../api/api";
import { useNavigate } from "react-router-dom";
import "./Register.css";

export default function Register() {
  const navigate = useNavigate();

  const [form, setForm] = useState({
    name: "",
    phone: "",
    email: "",
    role: "buyer",
    password: "",
    confirm_password: "",
  });

  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState<Record<string, string>>({});

  const handleChange = (field: string, value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
    if (errors[field]) setErrors((prev) => ({ ...prev, [field]: "" }));
  };

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    if (!form.name.trim()) newErrors.name = "Please enter your full name";
    if (!form.phone.trim()) newErrors.phone = "Phone number is required";
    if (!form.email.trim()) newErrors.email = "Email address is required";
    if (!form.email.match(/^\S+@\S+\.\S+$/)) newErrors.email = "Enter a valid email address";
    if (!form.password) newErrors.password = "Password is required";
    if (form.password.length < 6) newErrors.password = "Password must be at least 6 characters";
    if (form.password !== form.confirm_password)
      newErrors.confirm_password = "Passwords do not match";
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (loading) return;
    if (!validateForm()) return;

    setLoading(true);
    try {
      const res = await registerUser(form);
      alert(res.message || " OTP sent to your email. Please verify.");
      // redirect to OTP screen with email
      navigate("/verify-otp", { state: { email: form.email } });
    } catch (error: any) {
      console.error("Registration error:", error);
      alert(" Registration failed: " + (error.message || "Please try again"));
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="register-form-container">
      <h2>Create Account</h2>
      <p className="register-form-subtitle">Join thousands of users today</p>

      <form onSubmit={handleSubmit}>
        <FormInput label="Full Name" value={form.name}
          onChange={(e: ChangeEvent<HTMLInputElement>) => handleChange("name", e.target.value)}
          placeholder="Enter your full name" />
        {errors.name && <div className="register-error-message">{errors.name}</div>}

        <FormInput label="Phone Number" type="tel" value={form.phone}
          onChange={(e: ChangeEvent<HTMLInputElement>) => handleChange("phone", e.target.value)}
          placeholder="+1 (555) 123-4567" />
        {errors.phone && <div className="register-error-message">{errors.phone}</div>}

        <FormInput label="Email Address" type="email" value={form.email}
          onChange={(e: ChangeEvent<HTMLInputElement>) => handleChange("email", e.target.value)}
          placeholder="your@email.com" />
        {errors.email && <div className="register-error-message">{errors.email}</div>}

        <label className="register-label">Account Type</label>
        <RoleSelector value={form.role}
          onChange={(e: ChangeEvent<HTMLSelectElement>) => handleChange("role", e.target.value)} />
        <div className="register-role-hint">
          {form.role === "buyer"
            ? "Browse and purchase products"
            : "Sell your products to buyers"}
        </div>

        <FormInput label="Password" type="password" value={form.password}
          onChange={(e: ChangeEvent<HTMLInputElement>) => handleChange("password", e.target.value)}
          placeholder="Create a secure password" />
        {errors.password && <div className="register-error-message">{errors.password}</div>}

        <FormInput label="Confirm Password" type="password" value={form.confirm_password}
          onChange={(e: ChangeEvent<HTMLInputElement>) =>
            handleChange("confirm_password", e.target.value)
          }
          placeholder="Re-enter your password" />
        {errors.confirm_password && (
          <div className="register-error-message">{errors.confirm_password}</div>
        )}

        <button type="submit" className="register-submit-btn" disabled={loading}>
          {loading ? "Sending OTP..." : "Create Account"}
        </button>
      </form>

      <p className="register-login-link">
        Already have an account?{" "}
        <span
          className="register-link"
          onClick={() => navigate("/login")}
          tabIndex={0}
          onKeyPress={(e) => e.key === "Enter" && navigate("/login")}
        >
          Sign in here
        </span>
      </p>
    </div>
  );
}
