// src/pages/buyer/Home.tsx
import React, { useEffect, useState, JSX } from "react";
import { useNavigate } from "react-router-dom";

import Header from "../../components/buyer/Header";
import Sidebar from "../../components/buyer/Sidebar";
import BannerAd from "../../components/buyer/Banner";
import Categories from "../../components/buyer/Categories";
import RecentlyViewed from "../../components/buyer/RecentlyViewed";
import ProductsList from "../../components/buyer/ProductsList";
import UserProfile from "../../components/buyer/UserProfile";
import ProductDetails from "../../components/buyer/ProductDetails";
import CartPanel from "../../components/buyer/CartPanel";
import WishlistPanel from "../../components/buyer/WishlistPanel";
import NotificationsPanel from "../../components/buyer/NotificationsPanel";
import OrdersList from "../../components/buyer/OrdersList";
import PaymentPanel from "../../components/buyer/PaymentPanel";
import Chat from "../../components/buyer/ChatPanel";
import QuotationsPanel from "../../components/buyer/QuotationsPanel";

import {
  getProfile,
  getProducts,
  getCart,
  addToCart,
  clearCartItem, //  new import
  removeCartItem,
  getWishlist,
  toggleWishlist,
  searchProducts,
  getNotifications,
  markNotificationRead,
} from "../../api/api";

import { Product, ProductVariant, CartItem, Notification, WishlistItem } from "../../types";
import "./Home.css";

// Default image for missing product thumbnails
const DEFAULT_IMAGE = "https://via.placeholder.com/150";
const BACKEND_URL = process.env.NODE_ENV === "production" ? "" : "http://127.0.0.1:8000";

// Normalize product structure for display (image URL + ensure variants array)
const normalizeProducts = (products: Product[]): Product[] =>
  products.map((p) => ({
    ...p,
    image: p.image
      ? p.image.startsWith("http")
        ? p.image
        : `${BACKEND_URL}${p.image}`
      : DEFAULT_IMAGE,
    variants: p.variants || [],
  }));

export default function BuyerHome() {
  const navigate = useNavigate();

  const [token, setToken] = useState<string | null>(null);
  const [userEmail, setUserEmail] = useState<string>("");

  const [products, setProducts] = useState<Product[]>([]);
  const [searchResults, setSearchResults] = useState<Product[]>([]);
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [loading, setLoading] = useState(true);
  const [searching, setSearching] = useState(false);

  const [cartItems, setCartItems] = useState<CartItem[]>([]);
  const [wishlist, setWishlist] = useState<Product[]>([]);
  const [notifications, setNotifications] = useState<Notification[]>([]);

  const [activePanel, setActivePanel] = useState<
    | "home"
    | "cart"
    | "wishlist"
    | "orders"
    | "notifications"
    | "payment"
    | "chat"
    | "profile"
    | "quotations"
  >("home");

  const [conversationId, setConversationId] = useState<number | null>(null);

  // -----------------------------------------------------
  // Initial Fetch
  // -----------------------------------------------------
  useEffect(() => {
    const fetchInitialData = async () => {
      try {
        const storedToken = localStorage.getItem("token");
        if (!storedToken) {
          navigate("/login");
          return;
        }
        setToken(storedToken);

        const profileData = await getProfile(storedToken);
        setUserEmail(profileData.email || "");

        const productsData: Product[] = await getProducts(storedToken);
        setProducts(normalizeProducts(productsData));

        const cartData: CartItem[] = await getCart(storedToken);
        setCartItems(cartData);

        const wishlistData: WishlistItem[] = await getWishlist(storedToken);
        setWishlist(normalizeProducts(wishlistData.map((w) => w.product)));

        const notifs: Notification[] = await getNotifications(storedToken);
        setNotifications(notifs);
      } catch (error) {
        console.error(error);
        alert("Session expired. Please log in again.");
        localStorage.clear();
        navigate("/login");
      } finally {
        setLoading(false);
      }
    };

    fetchInitialData();
  }, [navigate]);

  // -----------------------------------------------------
  // CART
  // -----------------------------------------------------
  const refreshCart = async () => {
    if (!token) return;
    const cartData = await getCart(token);
    setCartItems(cartData);
  };

  // Keep the signature aligned with ProductDetails (variant object, not id)
  const handleAddToCart = async (
    product: Product,
    quantity: number,
    variant?: ProductVariant
  ) => {
    if (!token) return;
    try {
      // API expects (token, productId: number, quantity: number, variantId?: number | null)
      await addToCart(token, product.id, quantity, variant?.id ?? null);
      await refreshCart();
    } catch (err) {
      console.error(err);
      alert("Could not update cart.");
    }
  };

  // -----------------------------------------------------
  // WISHLIST
  // -----------------------------------------------------
  const handleToggleWishlist = async (product: Product) => {
    if (!token) return;
    try {
      const isInWishlist = wishlist.some((p) => p.id === product.id);
      await toggleWishlist(token, product.id.toString(), isInWishlist);
      const updatedWishlist: WishlistItem[] = await getWishlist(token);
      setWishlist(normalizeProducts(updatedWishlist.map((w) => w.product)));
    } catch (err) {
      console.error("Error updating wishlist:", err);
    }
  };

  // -----------------------------------------------------
  // SEARCH
  // -----------------------------------------------------
  const handleSearch = async (query: string) => {
    if (!query) {
      setSearching(false);
      setSearchResults([]);
      setActivePanel("home");
      return;
    }

    try {
      setSearching(true);
      const data: Product[] = await searchProducts(query);
      setSearchResults(normalizeProducts(data));
      setActivePanel("home");
    } catch (err) {
      console.error("Search failed:", err);
      alert("Error while searching. Please try again.");
    }
  };

  // -----------------------------------------------------
  // NOTIFICATIONS
  // -----------------------------------------------------
  const handleMarkNotificationRead = async (id: number) => {
    if (!token) return;
    try {
      await markNotificationRead(token, id);
      setNotifications((prev) =>
        prev.map((n) => (n.id === id ? { ...n, is_read: true } : n))
      );
    } catch (err) {
      console.error("Failed to mark as read:", err);
    }
  };

  // -----------------------------------------------------
  // BODY CONTENT SWITCH
  // -----------------------------------------------------
  let bodyContent: JSX.Element | null = null;

  if (selectedProduct) {
    bodyContent = (
      <ProductDetails
        product={selectedProduct}
        onBack={() => setSelectedProduct(null)}
        onAddToCart={handleAddToCart}
        token={token!}
      />
    );
  }  else if (activePanel === "cart") {
  bodyContent = (
    <CartPanel
      products={cartItems}
      onClose={() => setActivePanel("home")}
      onCheckout={() => setActivePanel("payment")}
      onUpdateQuantity={async (cartItemId, productId, qty) => {
        if (!token) return;

        const current = cartItems.find((c) => c.id === cartItemId);
        const variantId = current?.variant?.id ?? null;

        if (qty <= 0) {
          await removeCartItem(token, cartItemId);
        } else {
          await addToCart(token, productId, qty, variantId);
        }
        await refreshCart();
      }}
      onClearItem={async (cartItemId) => {     //  new handler
        if (!token) return;
        await clearCartItem(token, cartItemId);
        await refreshCart();
      }}
    />
  );
}

else if (activePanel === "wishlist") {
    bodyContent = (
      <WishlistPanel
        products={wishlist}
        onClose={() => setActivePanel("home")}
        onToggleWishlist={handleToggleWishlist}
        onSelectProduct={setSelectedProduct}
      />
    );
  } else if (activePanel === "orders") {
    bodyContent = (
      <OrdersList
        token={token!}
        onClose={() => setActivePanel("home")}
        onStartChat={(cid) => {
          setConversationId(cid ?? null);
          setActivePanel("chat");
        }}
      />
    );
  } else if (activePanel === "notifications") {
    bodyContent = (
      <NotificationsPanel
        token ={token}
        onClose={() => setActivePanel("home")}
        
      />
    );
  } else if (activePanel === "payment") {
    bodyContent = (
      <PaymentPanel
        token={token!}
        cartItems={cartItems}
        onClose={() => setActivePanel("orders")}
      />
    );
  } else if (activePanel === "chat") {
    bodyContent = (
      <Chat
        token={token!}
        conversationId={conversationId}
        userEmail={userEmail}
        onClose={() => setActivePanel("home")}
      />
    );
  } else if (activePanel === "profile") {
    bodyContent = (
      <UserProfile token={token!} onClose={() => setActivePanel("home")} />
    );
  } else if (activePanel === "quotations") {
    bodyContent = (
      <QuotationsPanel token={token!} onClose={() => setActivePanel("home")} />
    );
  } else if (searching && searchResults.length > 0) {
    bodyContent = (
      <ProductsList
        products={searchResults}
        onSelectProduct={setSelectedProduct}
        onToggleWishlist={handleToggleWishlist}
        wishlist={wishlist}
      />
    );
  } else {
    bodyContent = (
      <div className="scroll-content">
        {!selectedCategory && <BannerAd />}
        {!selectedCategory && (
          <>
            <Categories
              token={token!}
              products={products}
              onSelectCategory={setSelectedCategory}
            />
            <RecentlyViewed
              products={products}
              onSelectProduct={setSelectedProduct}
              onToggleWishlist={handleToggleWishlist}
              wishlist={wishlist}
            />
          </>
        )}
        {selectedCategory && (
          <ProductsList
            products={products.filter((p) => p.category === selectedCategory)}
            onSelectProduct={setSelectedProduct}
            onToggleWishlist={handleToggleWishlist}
            wishlist={wishlist}
          />
        )}
      </div>
    );
  }

  if (loading) return <div className="center">Loading...</div>;
  if (!token) return null;

  return (
    <div className="home-container">
      <Header
        onUserPress={() => setActivePanel("profile")}
        onCartPress={() => setActivePanel("cart")}
        onSearch={handleSearch}
        cartCount={cartItems.reduce((sum, item) => sum + item.quantity, 0)}
      />
      <div className="home-body">
        <Sidebar
          onWishlistPress={() => setActivePanel("wishlist")}
          onOrdersPress={() => setActivePanel("orders")}
          onChatPress={() => setActivePanel("chat")}
          // onNotificationsPress={() => setActivePanel("notifications")}
          onQuotationsPress={() => setActivePanel("quotations")}
        />
        <div className="home-content">{bodyContent}</div>
      </div>
    </div>
  );
}
