import React, { useState } from "react";
import "./UploadProduct.css";
import { uploadProductsExcel } from "../../api/api";

type Props = {
  token: string;
  onClose: () => void;
  onSuccess: () => void;
};

const UploadProduct: React.FC<Props> = ({ token, onClose, onSuccess }) => {
  const [file, setFile] = useState<File | null>(null);
  const [uploading, setUploading] = useState(false);
  const [message, setMessage] = useState<string | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0] || null;
    setFile(selectedFile);
    setMessage(null);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!file) {
      setMessage("Please select an Excel file first.");
      return;
    }

    try {
      setUploading(true);
      const res = await uploadProductsExcel(token, file);
      setMessage(` Uploaded successfully! Created ${res.created} products.`);
      onSuccess();
    } catch (err: any) {
      setMessage(` Error: ${err.message}`);
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className="uploadproduct-container">
      <div className="uploadproduct-card">
        <h2> Upload Products (Excel)</h2>
        <form onSubmit={handleSubmit}>
          <input
            type="file"
            accept=".xlsx, .xls"
            onChange={handleFileChange}
            disabled={uploading}
          />
          <button
            type="submit"
            className="uploadproduct-btn"
            disabled={!file || uploading}
          >
            {uploading ? "Uploading..." : "Upload"}
          </button>
          <button type="button" className="uploadproduct-cancel" onClick={onClose}>
            Cancel
          </button>
        </form>

        {message && <p className="uploadproduct-message">{message}</p>}

        <div className="uploadproduct-info">
          <p><strong>Expected Excel columns:</strong></p>
          <ul>
            <li>name, ref_no, description, category, hsn, discount, brand, cas_no, variant_label, est_price, price</li>
          </ul>
          <p>You can include multiple rows per product (same ref_no) for multiple variants.</p>
        </div>
      </div>
    </div>
  );
};

export default UploadProduct;
