import React from "react";
import { Search, Bell, User } from "lucide-react";
import "./Header.css";

interface HeaderProps {
  onUserPress: () => void;
  onCartPress?: () => void;
  onNotificationsPress?: () => void;
  unreadCount?: number;
}

const Header: React.FC<HeaderProps> = ({
  onUserPress,
  onNotificationsPress,
  unreadCount = 0,
}) => {
  return (
    <div className="header-container">
      {/* Logo */}
      <img
        src="https://upload.wikimedia.org/wikipedia/commons/a/ab/Logo_TV_2015.png"
        alt="logo"
        className="header-logo"
      />

      {/* Search Bar */}
      <div className="header-search">
        <Search size={18} color="#888" className="search-icon" />
        <input
          type="text"
          placeholder="Search products, chemicals..."
          className="search-input"
        />
      </div>

      {/* Notifications */}
      <div className="header-icon" onClick={onNotificationsPress}>
        <Bell size={24} color="#333" />
        {unreadCount > 0 && (
          <div className="badge">{unreadCount}</div>
        )}
      </div>

      {/* User */}
      <div className="header-icon" onClick={onUserPress}>
        <User size={24} color="#333" />
      </div>
    </div>
  );
};

export default Header;