import React from "react";
import { Heart, X, ShoppingCart, Eye } from "lucide-react";
import "./WishlistPanel.css";
import { Product } from "../../types";

type Props = {
  products: Product[];
  onClose: () => void;
  onToggleWishlist: (product: Product) => void;
  onSelectProduct?: (product: Product) => void;
  onAddToCart?: (product: Product, variantId?: number) => void;
};

export default function WishlistPanel({
  products,
  onClose,
  onToggleWishlist,
  onSelectProduct,
  onAddToCart,
}: Props) {
  const DEFAULT_IMAGE = "https://via.placeholder.com/300";

  const safeProducts = products.map((p) => {
    const baseVariant =
      p.variants && p.variants.length > 0 ? p.variants[0] : null;
    const basePrice =
      baseVariant?.price && baseVariant.price > 0
        ? baseVariant.price
        : baseVariant?.est_price && baseVariant.est_price > 0
        ? baseVariant.est_price
        : null;

    const discount = p.discount || 0;
    const discountedPrice =
      basePrice && discount > 0
        ? basePrice - (basePrice * discount) / 100
        : basePrice;

    return {
      ...p,
      basePrice,
      discountedPrice,
      image:
        p.image && p.image.startsWith("http") ? p.image : DEFAULT_IMAGE,
    };
  });

  return (
    <div className="wishlist-overlay" onClick={onClose}>
      <div className="wishlist-panel" onClick={(e) => e.stopPropagation()}>
        {/* Header */}
        <div className="wishlist-header">
          <div className="wishlist-header-content">
            <h2>My Wishlist</h2>
            <p>Your favorite products in one place</p>
          </div>
          <button className="wishlist-close-btn" onClick={onClose}>
            <X size={20} />
          </button>
        </div>

        {/* Summary */}
        <div className="wishlist-summary">
          <div className="wishlist-summary-item">
            <span className="wishlist-summary-label">Total Items</span>
            <span className="wishlist-summary-value">
              {safeProducts.length}
            </span>
          </div>
          <div className="wishlist-summary-item">
            <span className="wishlist-summary-label">Estimated Total</span>
            <span className="wishlist-summary-value">
              $
              {safeProducts
                .reduce(
                  (sum, item) => sum + (item.discountedPrice || 0),
                  0
                )
                .toFixed(2)}
            </span>
          </div>
        </div>

        {/* Content */}
        {safeProducts.length === 0 ? (
          <div className="wishlist-empty">
            <div className="wishlist-empty-icon"></div>
            <h3>Your wishlist is empty</h3>
            <p>Start adding products you love</p>
          </div>
        ) : (
          <div className="wishlist-content">
            <div className="wishlist-grid">
              {safeProducts.map((item) => (
                <div key={item.id} className="wishlist-card">
                  <div className="wishlist-card-header">
                    <button
                      className="wishlist-remove-btn"
                      onClick={() => onToggleWishlist(item)}
                    >
                      <Heart size={18} fill="currentColor" />
                    </button>
                    {onSelectProduct && (
                      <button
                        className="wishlist-view-btn"
                        onClick={() => onSelectProduct(item)}
                      >
                        <Eye size={18} />
                      </button>
                    )}
                  </div>

                  {/* Image */}
                  <div
                    className="wishlist-image-container"
                    onClick={() => onSelectProduct?.(item)}
                  >
                    <img
                      src={item.image || DEFAULT_IMAGE}
                      alt={item.name}
                      className="wishlist-image"
                    />
                    {item.discount && item.discount > 0 && (
                      <div className="wishlist-discount-badge">
                        -{item.discount}%
                      </div>
                    )}
                    <div className="wishlist-image-overlay">
                      <span>View Product</span>
                    </div>
                  </div>

                  {/* Body */}
                  <div className="wishlist-card-body">
                    <h3 className="wishlist-product-name">
                      {item.name || "Unnamed Product"}
                    </h3>
                    <p className="wishlist-product-category">
                      {item.category || "Uncategorized"}
                    </p>
                    <div className="wishlist-product-price">
                      {item.basePrice ? (
                        item.discount && item.discount > 0 ? (
                          <>
                            <span className="discounted-price">
                              ${item.discountedPrice?.toFixed(2)}
                            </span>
                            <span className="original-price">
                              ${item.basePrice?.toFixed(2)}
                            </span>
                          </>
                        ) : (
                          <span className="regular-price">
                            ${item.basePrice?.toFixed(2)}
                          </span>
                        )
                      ) : (
                        <span className="none">Out of Stock</span>
                      )}
                    </div>
                  </div>

                  {/* Footer */}
                  <div className="wishlist-card-footer">
                    <button
                      className="wishlist-add-to-cart-btn"
                      disabled={!item.basePrice}
                      onClick={() => onAddToCart?.(item, item.variants?.[0]?.id)}
                    >
                      <ShoppingCart size={16} />
                      Add to Cart
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Footer */}
        <div className="wishlist-footer">
          <button className="wishlist-close-main-btn" onClick={onClose}>
            Close Wishlist
          </button>
        </div>
      </div>
    </div>
  );
}
