// Header.tsx
import React, { useState, KeyboardEvent } from "react";
import { Search, ShoppingCart, User } from "lucide-react";
import "./Header.css";

type HeaderProps = {
  onUserPress: () => void;
  onCartPress: () => void;
  onSearch: (query: string) => void;
  cartCount?: number;
};

const Header = ({ onUserPress, onCartPress, onSearch, cartCount = 0 }: HeaderProps) => {
  const [searchText, setSearchText] = useState("");

  const handleKeyPress = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") {
      e.preventDefault();
      onSearch(searchText.trim());
    }
  };

  return (
    <header className="header-container">
      {/* Logo */}
      <div className="header-logo-section">
        <img
          src="https://upload.wikimedia.org/wikipedia/commons/a/ab/Logo_TV_2015.png"
          alt="Logo"
          className="header-logo"
        />
        <span className="header-title">Chemical Marketplace</span>
      </div>

      {/* Search Bar */}
      <div className="search-container">
        <Search size={20} className="search-icon" />
        <input
          type="text"
          placeholder="Search products or CAS No..."
          className="search-input"
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
          onKeyDown={handleKeyPress}
        />
      </div>

      {/* Cart & User Icons */}
      <div className="icon-group">
        <button className="icon-button" onClick={onCartPress}>
          <ShoppingCart size={24} />
          {cartCount > 0 && <span className="cart-badge">{cartCount}</span>}
        </button>

        <button className="icon-button" onClick={onUserPress}>
          <User size={28} />
        </button>
      </div>
    </header>
  );
};

export default Header;
