import React, { useState } from "react";
import { CartItem } from "../../types";
import "./PaymentPanel.css";
import {
  placeOrder,
  initiatePaytmPayment,
  getPaytmPaymentUrl,
} from "../../api/api";

type Props = {
  token: string;
  cartItems: CartItem[];
  onClose: () => void;
  onOrderPlaced?: () => void;
};

type OrderResponse = { id: string };
type PaytmInitResponse = { txnToken: string; orderId: string };

export default function PaymentPanel({
  token,
  cartItems,
  onClose,
  onOrderPlaced,
}: Props) {
  const [loading, setLoading] = useState(false);

  //  Compute total using the *selected variant* in each cart item
  const total = cartItems.reduce((sum, item) => {
    const variant = item.variant ?? null;
    const price = Number(variant?.price ?? variant?.est_price ?? 0);
    return sum + price * item.quantity;
  }, 0);

  const handlePaytmPayment = async () => {
    if (cartItems.length === 0) return alert("Cart is empty");
    setLoading(true);

    try {
      // 1 Prepare order items
      const items = cartItems.map((ci) => {
        const variant = ci.variant ?? null;
        const price = Number(variant?.price ?? variant?.est_price ?? 0);

        return {
          product_id: String(ci.product.id),
          variant_id: variant ? String(variant.id) : undefined, //  actual variant from cart
          quantity: ci.quantity,
          price,
        };
      });

      // 2 Place the order
      const orderData: OrderResponse = await placeOrder(token, items);
      const orderId = orderData.id;

      // 3 Initiate Paytm
      const initData: PaytmInitResponse = await initiatePaytmPayment(
        token,
        orderId
      );
      const txnToken = initData.txnToken;
      if (!txnToken) throw new Error("No txnToken returned");

      // 4 Redirect user to Paytm gateway
      const paytmPaymentUrl = getPaytmPaymentUrl(orderId, txnToken);
      window.open(paytmPaymentUrl, "_blank");
      alert("Payment page opened in a new tab. Complete payment there.");

      if (onOrderPlaced) onOrderPlaced();
      onClose();
    } catch (err) {
      console.error(err);
      alert("Failed to place order or initiate Paytm payment.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="payment-overlay">
      <div className="payment-panel">
        <h2>Checkout</h2>

        {cartItems.length === 0 ? (
          <p className="empty-text">Your cart is empty</p>
        ) : (
          <div className="cart-items">
            {cartItems.map((item) => {
              const variant = item.variant ?? null;
              const price = Number(variant?.price ?? variant?.est_price ?? 0);

              return (
                <div className="cart-row" key={item.id || item.product.id}>
                  <span>
                    {item.product.name}
                    {variant?.variant_label ? ` (${variant.variant_label})` : ""}
                  </span>
                  <span>
                    {price.toFixed(2)}  {item.quantity} = 
                    {(price * item.quantity).toFixed(2)}
                  </span>
                </div>
              );
            })}
          </div>
        )}

        <p className="total">Total: {total.toFixed(2)}</p>

        <div className="payment-actions">
          <button className="close-btn" onClick={onClose}>
            Cancel
          </button>
          <button
            className="pay-btn"
            onClick={handlePaytmPayment}
            disabled={loading}
          >
            {loading ? "Processing..." : "Invoice method"}
          </button>
        </div>
      </div>
    </div>
  );
}
