import React from "react";
import { Heart } from "lucide-react";
import "./ProductsList.css";
import { Product, ProductVariant } from "../../types"; //  import from your shared types

type Props = {
  products: Product[];
  onSelectProduct: (product: Product) => void;
  onToggleWishlist: (product: Product) => void;
  wishlist: Product[];
  cardsPerRow?: number;
  loading?: boolean;
};

const DEFAULT_IMAGE = "https://via.placeholder.com/150";

export default function ProductsList({
  products,
  onSelectProduct,
  onToggleWishlist,
  wishlist,
  cardsPerRow = 5,
  loading = false,
}: Props) {
  if (loading) {
    return (
      <div
        className="productslist-products-container"
        style={{ gridTemplateColumns: `repeat(${cardsPerRow}, 1fr)` }}
      >
        {[...Array(cardsPerRow)].map((_, index) => (
          <div key={index} className="productslist-product-card productslist-skeleton">
            <div className="productslist-skeleton-image"></div>
            <div className="productslist-product-info">
              <div className="productslist-skeleton-name"></div>
              <div className="productslist-skeleton-price"></div>
            </div>
          </div>
        ))}
      </div>
    );
  }

  if (!products || products.length === 0) {
    return (
      <div className="productslist-products-container">
        <div className="productslist-empty-state">
          <div className="productslist-empty-icon"></div>
          <div className="productslist-empty-text">No products found</div>
          <div className="productslist-empty-subtext">Try adjusting your search or filters</div>
        </div>
      </div>
    );
  }

  return (
    <div
      className="productslist-products-container"
      style={{ gridTemplateColumns: `repeat(${cardsPerRow}, 1fr)` }}
    >
      {products.map((item) => {
        const isWishlisted = wishlist.some((p) => p.id === item.id);

        //  Determine which variant to show (first variant or best priced one)
        const variant: ProductVariant | undefined = item.variants?.[0];
        const price = Number(variant?.price ?? variant?.est_price ?? 0);

        const imageUri = item.image || DEFAULT_IMAGE;

        return (
          <div key={item.id} className="productslist-product-card">
            <div
              className="productslist-image-container"
              onClick={() => onSelectProduct(item)}
            >
              <img
                src={imageUri}
                alt={item.name}
                className="productslist-product-image"
                onError={(e) => {
                  e.currentTarget.src = DEFAULT_IMAGE;
                }}
              />
            </div>

            <button
              className={`productslist-heart-btn ${isWishlisted ? "wishlisted" : ""}`}
              onClick={(e) => {
                e.stopPropagation();
                onToggleWishlist(item);
              }}
              aria-label={isWishlisted ? "Remove from wishlist" : "Add to wishlist"}
            >
              <Heart
                size={20}
                fill={isWishlisted ? "red" : "none"}
                color={isWishlisted ? "red" : "currentColor"}
              />
            </button>

            <div className="productslist-product-info">
              <div className="productslist-product-category">
                {item.category}
              </div>
              <div className="productslist-product-name" title={item.name}>
                {item.name}
              </div>

              {/*  Show variant label and price */}
              {variant ? (
                <div className="productslist-product-price">
                  {price.toFixed(2)}{" "}
                  <span className="variant-label">
                    {variant.variant_label ? `(${variant.variant_label})` : ""}
                  </span>
                </div>
              ) : (
                <div className="productslist-product-price unavailable">
                  Price unavailable
                </div>
              )}
            </div>
          </div>
        );
      })}
    </div>
  );
}
