import React, { useEffect, useState } from "react";
import {
  getQuotationRequests,
  getQuotationForRequest,
} from "../../api/api";
import "./QuotationsPanel.css";

export type QuotationRequest = {
  id: number;
  product: {
    id: number;
    name: string;
  };
  is_resolved: boolean;
  created_at: string;
};

export type Quotation = {
  file_url: string;
};

type Props = {
  token: string;
  onClose: () => void;
};

export default function QuotationsPanel({ token, onClose }: Props) {
  const [loading, setLoading] = useState(true);
  const [requests, setRequests] = useState<QuotationRequest[]>([]);
  const [quotationsMap, setQuotationsMap] = useState<Record<number, string>>({});

  const fetchRequests = async () => {
    try {
      setLoading(true);
      const data = await getQuotationRequests(token);
      setRequests(data);

      // Fetch each quotation
      const map: Record<number, string> = {};
      await Promise.all(
        data.map(async (req: QuotationRequest) => {
          try {
            const q = await getQuotationForRequest(token, req.id);
            map[req.id] = q.file_url;
          } catch {
            console.log(`No quotation yet for request ${req.id}`);
          }
        })
      );
      setQuotationsMap(map);
    } catch (err) {
      console.error(err);
      alert("Could not load quotation requests.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchRequests();
  }, []);

  const handleDownload = (url: string) => {
    window.open(url, "_blank");
  };

  const getStatusIcon = (fileUrl: string) => {
    return fileUrl ? "" : "";
  };

  const getStatusColor = (fileUrl: string) => {
    return fileUrl ? "#10b981" : "#f59e0b";
  };

  const getStatusText = (fileUrl: string) => {
    return fileUrl ? "Quotation Ready" : "Awaiting Seller";
  };

  if (loading) return (
    <div className="quotations-overlay">
      <div className="quotations-panel">
        <div className="quotations-loader">
          <div className="quotations-spinner"></div>
          Loading your quotations...
        </div>
      </div>
    </div>
  );

  return (
    <div className="quotations-overlay" onClick={onClose}>
      <div className="quotations-panel" onClick={(e) => e.stopPropagation()}>
        {/* Header */}
        <div className="quotations-header">
          <div className="quotations-header-content">
            <h2>My Quotations</h2>
            <p>Manage your price quotation requests</p>
          </div>
          <button className="quotations-close-btn" onClick={onClose}>
            
          </button>
        </div>

        {/* Summary */}
        <div className="quotations-summary">
          <div className="quotations-summary-item">
            <span className="quotations-summary-label">Total Requests</span>
            <span className="quotations-summary-value">{requests.length}</span>
          </div>
          <div className="quotations-summary-item">
            <span className="quotations-summary-label">Ready</span>
            <span className="quotations-summary-value">
              {Object.keys(quotationsMap).length}
            </span>
          </div>
          <div className="quotations-summary-item">
            <span className="quotations-summary-label">Pending</span>
            <span className="quotations-summary-value">
              {requests.length - Object.keys(quotationsMap).length}
            </span>
          </div>
        </div>

        {requests.length === 0 ? (
          <div className="quotations-empty">
            <div className="quotations-empty-icon"></div>
            <h3>No quotation requests</h3>
            <p>Your quotation requests will appear here once you request prices from sellers</p>
          </div>
        ) : (
          <div className="quotations-content">
            <div className="quotations-grid">
              {requests.map((req) => {
                const fileUrl = quotationsMap[req.id];
                return (
                  <div key={req.id} className="quotations-card">
                    <div className="quotations-card-header">
                      <div className="quotations-product-info">
                        <h3 className="quotations-product-name">{req.product?.name}</h3>
                        <div 
                          className="quotations-status-badge"
                          style={{ backgroundColor: getStatusColor(fileUrl) }}
                        >
                          {getStatusIcon(fileUrl)} {getStatusText(fileUrl)}
                        </div>
                      </div>
                      <div className="quotations-request-id">
                        #Q-{req.id.toString().padStart(6, '0')}
                      </div>
                    </div>

                    <div className="quotations-card-body">
                      <div className="quotations-date-info">
                        <span className="quotations-date-icon"></span>
                        Requested on {new Date(req.created_at).toLocaleDateString('en-US', {
                          year: 'numeric',
                          month: 'long',
                          day: 'numeric',
                          hour: '2-digit',
                          minute: '2-digit'
                        })}
                      </div>

                      <div className="quotations-actions">
                        {fileUrl ? (
                          <button
                            className="quotations-download-btn"
                            onClick={() => handleDownload(fileUrl)}
                          >
                            <span className="quotations-download-icon"></span>
                            Download Quotation
                          </button>
                        ) : (
                          <div className="quotations-pending">
                            <div className="quotations-pending-icon"></div>
                            <div className="quotations-pending-text">
                              <strong>Waiting for seller</strong>
                              <span>Seller will upload quotation soon</span>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>

                    <div className="quotations-card-footer">
                      <div className="quotations-product-id">
                        Product ID: {req.product.id}
                      </div>
                      {fileUrl && (
                        <div className="quotations-file-info">
                          <span className="quotations-file-icon"></span>
                          PDF Document
                        </div>
                      )}
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        )}

        <div className="quotations-footer">
          <button className="quotations-close-main-btn" onClick={onClose}>
            Close Quotations
          </button>
        </div>
      </div>
    </div>
  );
}