import React from "react";
import "./Sidebar.css";

interface SidebarProps {
  onDashboardPress: () => void;
  onChatPress: () => void;
  onQuotationsPress: () => void;
  onOrdersPress: () => void;
}

export default function Sidebar({
  onDashboardPress,
  onChatPress,
  onQuotationsPress,
  onOrdersPress,
}: SidebarProps) {
  const handleLogout = () => {
    localStorage.removeItem("access");
    localStorage.removeItem("refresh");
    window.location.href = "/login";
  };

  return (
    <div className="sidebar">
      <div className="sidebar-top">
        <button className="sidebar-item" onClick={onDashboardPress}>
          Dashboard
        </button>

        <button className="sidebar-item" onClick={onOrdersPress}>
          Orders
        </button>

        <button className="sidebar-item" onClick={onQuotationsPress}>
          Quotations
        </button>

        <button className="sidebar-item chat-btn" onClick={onChatPress}>
          Chat
        </button>

        <button className="sidebar-item logout-btn" onClick={handleLogout}>
          Logout
        </button>
      </div>
    </div>
  );
}
