import React from "react";
import "./ProductsList.css";

type ProductVariant = {
  id?: string;
  variant_label: string; // e.g. "1 Liter", "2 kg", "Box of 10"
  est_price: number;
  price?: number | null;
  created_at?: string;
};

export type Product = {
  id: string;
  name: string;
  ref_no: string;
  description?: string;
  category: string;
  image?: string | null;
  hsn?: string;
  discount?: number;
  brand: string;
  cas_no?: string;
  seller: string;
  created_at: string;
  updated_at: string;
  variants: ProductVariant[];
  average_rating?: number | null;
  total_reviews?: number;
};

interface Props {
  products: Product[];
  onSelectProduct: React.Dispatch<React.SetStateAction<Product | null>>;
  cardsPerRow?: number;
}

export default function ProductsList({
  products,
  onSelectProduct,
  cardsPerRow = 4,
}: Props) {
  const cardWidth = `${100 / cardsPerRow - 1}%`;

  //  Default placeholder image (change URL if you prefer)
  const defaultImage =
    "https://cdn-icons-png.flaticon.com/512/1261/1261163.png"; // simple product placeholder icon

  if (products.length === 0) {
    return <div className="productslist-empty-state">No products found</div>;
  }

  return (
    <div className="productslist-products-container">
      {products.map((item) => {
        const imageUri = item.image ? `${item.image}?v=${item.id}` : defaultImage;

        //  Use only true prices
        const truePrices = item.variants
          .map((v) => v.price)
          .filter((p): p is number => p !== null && p !== undefined && p > 0);

        const lowestPrice =
          truePrices.length > 0 ? Math.min(...truePrices) : 0;

        const hasDiscount = !!(item.discount && item.discount > 0);
        const discountedPrice = hasDiscount
          ? lowestPrice - (lowestPrice * (item.discount ?? 0)) / 100
          : lowestPrice;

        const totalVariants = item.variants.length;

        const displayPrice =
          lowestPrice > 0 ? `$${lowestPrice.toFixed(2)}` : "None";

        const displayDiscountedPrice =
          hasDiscount && lowestPrice > 0
            ? `$${discountedPrice.toFixed(2)}`
            : null;

        return (
          <div
            key={item.id}
            className="productslist-product-card compact"
            style={{ width: cardWidth }}
            onClick={() => onSelectProduct(item)}
          >
            {/*  Product Image */}
            <img
              src={imageUri}
              alt={item.name}
              className="productslist-product-image"
              onError={(e) => {
                e.currentTarget.src = defaultImage; // fallback to default if image fails
              }}
            />

            {/*  Product Info */}
            <div className="productslist-product-info">
              <div className="productslist-product-name" title={item.name}>
                {item.name.length > 25
                  ? `${item.name.slice(0, 25)}`
                  : item.name}
              </div>

              <div className="productslist-product-brand">{item.brand}</div>

              {/*  Price Display */}
              <div className="productslist-product-price">
                {lowestPrice === 0 ? (
                  <span className="productslist-none-price">None</span>
                ) : hasDiscount && displayDiscountedPrice ? (
                  <>
                    <span className="productslist-old-price">
                      {displayPrice}
                    </span>
                    <span className="productslist-discounted-price">
                      {displayDiscountedPrice}
                    </span>
                  </>
                ) : (
                  <span className="productslist-normal-price">
                    {displayPrice}
                  </span>
                )}
              </div>

              {/*  Variants &  Rating */}
              <div className="productslist-meta">
                <span className="productslist-stock">
                   {totalVariants} variant
                  {totalVariants !== 1 ? "s" : ""}
                </span>

                {item.average_rating ? (
                  <span className="productslist-rating">
                     {item.average_rating.toFixed(1)}{" "}
                    <small>({item.total_reviews ?? 0})</small>
                  </span>
                ) : (
                  <span className="productslist-rating-muted">
                    No reviews yet
                  </span>
                )}
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}

