import React, { useEffect, useState } from "react";
import { getSellerOrders, updateOrderStatus } from "../../api/api";
import "./OrderPanel.css";

interface OrderItem {
  id: number;
  product: { id: number; name: string };
  quantity: number;
  price: number;
}

interface Order {
  id: number;
  buyer: number;
  status: string;
  total_price: string;
  created_at: string;
  items: OrderItem[];
}

interface OrderPanelProps {
  token: string;
  onClose: () => void;
}

export default function OrderPanel({ token, onClose }: OrderPanelProps) {
  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(true);

  const fetchOrders = async () => {
    try {
      const data = await getSellerOrders(token);
      setOrders(data);
    } catch (err) {
      console.error("Failed to fetch seller orders:", err);
    } finally {
      setLoading(false);
    }
  };

  const handleStatusChange = async (orderId: number, newStatus: string) => {
    try {
      await updateOrderStatus(token, orderId, newStatus);
      setOrders((prev) =>
        prev.map((o) => (o.id === orderId ? { ...o, status: newStatus } : o))
      );
      alert(`Order #${orderId} status updated to ${newStatus}`);
    } catch (err) {
      alert("Failed to update status");
      console.error(err);
    }
  };

  useEffect(() => {
    fetchOrders();
  }, []);

  if (loading) return <div className="center"><div className="loader"></div></div>;

  return (
    <div className="orders-panel">
      <div className="orders-header">
        <h2>Received Orders</h2>
        <button className="close-btn" onClick={onClose}></button>
      </div>

      {orders.length === 0 ? (
        <p>No orders yet.</p>
      ) : (
        orders.map((order) => (
          <div key={order.id} className="order-card">
            <div className="order-info">
              <h3>Order #{order.id}</h3>
              <p><strong>Status:</strong> {order.status}</p>
              <p><strong>Total:</strong> {order.total_price}</p>
              <p><strong>Placed on:</strong> {new Date(order.created_at).toLocaleString()}</p>
            </div>

            <div className="order-items">
              {order.items.map((item) => (
                <div key={item.id} className="order-item">
                  <span>{item.product.name}</span>
                  <span>x{item.quantity}</span>
                  <span>{item.price}</span>
                </div>
              ))}
            </div>

            <div className="order-actions">
              <select
                value={order.status}
                onChange={(e) => handleStatusChange(order.id, e.target.value)}
              >
                <option value="pending">Pending</option>
                <option value="processing">Processing</option>
                <option value="shipped">Shipped</option>
                <option value="delivered">Delivered</option>
                <option value="cancelled">Cancelled</option>
              </select>
            </div>
          </div>
        ))
      )}
    </div>
  );
}
