import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import Header from "../../components/seller/Header";
import Sidebar from "../../components/seller/Sidebar";
import BannerAd from "../../components/seller/BannerAd";
import ProductsList from "../../components/seller/ProductsList";
import UpdateProduct from "../../components/seller/UpdateProduct";
import AddProduct from "../../components/seller/AddProduct";
import UploadProduct from "../../components/seller/UploadProduct"; //  NEW
import SellerChat from "../../components/seller/ChatPanel";
import QuotationPanel from "../../components/seller/QuotationPanel";
import OrderPanel from "../../components/seller/OrderPanel";
import SellerDashboard from "../../components/seller/SellerDashboard";

import "./Home.css";
import { JSX } from "react/jsx-runtime";
import {
  getProducts,
  getNotifications,
  markNotificationRead,
  getQuotationRequests,
} from "../../api/api";

// ----------------- Types -----------------
type ProductVariant = {
  id?: string;
  variant_label: string;
  est_price: number;
  price?: number | null;
  created_at?: string;
};

export type Product = {
  id: string;
  name: string;
  ref_no: string;
  description?: string;
  category: string;
  image?: string | null;
  hsn?: string;
  discount?: number;
  brand: string;
  cas_no?: string;
  seller: string;
  created_at: string;
  updated_at: string;
  variants: ProductVariant[];
  average_rating?: number | null;
  total_reviews?: number;
};

type NotificationType = {
  id: number;
  message: string;
  is_read: boolean;
  created_at: string;
};

type QuotationRequest = {
  id: number;
  product: {
    id: number;
    name: string;
    seller: string;
  };
  buyer: string;
  created_at: string;
  is_resolved: boolean;
};

// ----------------- Component -----------------
export default function SellerHome() {
  const navigate = useNavigate();
  const [token, setToken] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);

  // Panels and data
  const [products, setProducts] = useState<Product[]>([]);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [showAddProduct, setShowAddProduct] = useState(false);
  const [showUploadProduct, setShowUploadProduct] = useState(false); //  NEW
  const [notifications, setNotifications] = useState<NotificationType[]>([]);
  const [showNotifications, setShowNotifications] = useState(false);
  const [showOrders, setShowOrders] = useState(false);
  const [showChat, setShowChat] = useState(false);
  const [showQuotations, setShowQuotations] = useState(false);
  const [quotationRequests, setQuotationRequests] = useState<QuotationRequest[]>([]);
  const [showDashboard, setShowDashboard] = useState(false);

  // ----------------- Constants -----------------
  const DEFAULT_IMAGE = "https://via.placeholder.com/150";
  const BACKEND_URL =
    process.env.NODE_ENV === "production"
      ? "https://linkzur-backend.onrender.com"
      : "http://127.0.0.1:8000";

  // ----------------- Fetch Products -----------------
  const fetchProductsHandler = async () => {
    try {
      if (!token) return;
      const data = await getProducts(token);

      const safeData = data.map((p: any) => {
        const imageUrl =
          p.image && p.image.startsWith("http")
            ? p.image
            : p.image
            ? `${BACKEND_URL}${p.image}`
            : DEFAULT_IMAGE;

        const minEstPrice =
          p.variants && p.variants.length > 0
            ? Math.min(...p.variants.map((v: any) => Number(v.est_price)))
            : 0;

        return { ...p, image: imageUrl, minEstPrice };
      });

      setProducts(safeData);
    } catch (error) {
      alert("Failed to fetch products");
      console.error(error);
    }
  };

  // ----------------- Fetch Notifications -----------------
  const fetchNotificationsHandler = async () => {
    try {
      if (!token) return;
      const data = await getNotifications(token);
      setNotifications(data);
    } catch (err) {
      console.error("Fetch notifications error:", err);
    }
  };

  const markUnreadAsRead = async () => {
    if (!token) return;
    const unreadIds = notifications.filter((n) => !n.is_read).map((n) => n.id);
    if (unreadIds.length === 0) return;

    try {
      await Promise.all(unreadIds.map((id) => markNotificationRead(token, id)));
      setNotifications((prev) =>
        prev.map((n) => (unreadIds.includes(n.id) ? { ...n, is_read: true } : n))
      );
    } catch (err) {
      console.error("Error marking notifications as read:", err);
    }
  };

  // ----------------- Fetch Quotation Requests -----------------
  const fetchQuotationRequestsHandler = async () => {
    try {
      if (!token) return;
      const data = await getQuotationRequests(token);
      setQuotationRequests(data);
    } catch (err) {
      alert("Failed to fetch quotation requests");
      console.error(err);
    }
  };

  // ----------------- Validate Token -----------------
  const validateToken = async () => {
    try {
      const storedToken = localStorage.getItem("token");
      if (!storedToken) throw new Error("No token");
      setToken(storedToken);
    } catch (error) {
      localStorage.clear();
      navigate("/Login");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    validateToken();
  }, []);

  useEffect(() => {
    if (token) {
      fetchProductsHandler();
      fetchNotificationsHandler();

      const interval = setInterval(fetchNotificationsHandler, 5000);
      return () => clearInterval(interval);
    }
  }, [token]);

  if (loading) {
    return (
      <div className="sellerhome-center">
        <div className="sellerhome-loader"></div>
      </div>
    );
  }

  const unreadCount = notifications.filter((n) => !n.is_read).length;

  // ----------------- Conditional Body Content -----------------
  let bodyContent: JSX.Element | null = null;

  if (showDashboard) {
    bodyContent = <SellerDashboard />;
  } else if (showChat && token) {
    bodyContent = (
      <SellerChat
        token={token}
        userEmail="seller@example.com"
        onClose={() => setShowChat(false)}
      />
    );
  } else if (showNotifications) {
    bodyContent = (
      <div className="sellerhome-notifications-container">
        <h2>Notifications</h2>
        {notifications.length === 0 ? (
          <p>No notifications yet.</p>
        ) : (
          notifications.map((n) => (
            <div key={n.id} className={`sellerhome-notification ${n.is_read ? "read" : "unread"}`}>
              <p>{n.message}</p>
              <span className="sellerhome-timestamp">{new Date(n.created_at).toLocaleString()}</span>
            </div>
          ))
        )}
      </div>
    );
  } else if (selectedProduct) {
    bodyContent = (
      <UpdateProduct
        token={token!}
        product={selectedProduct}
        onBack={() => setSelectedProduct(null)}
      />
    );
  } else if (showAddProduct) {
    bodyContent = (
      <AddProduct
        token={token!}
        onClose={() => {
          setShowAddProduct(false);
          fetchProductsHandler();
        }}
      />
    );
  } else if (showUploadProduct) {
    bodyContent = (
      <UploadProduct
        token={token!}
        onClose={() => setShowUploadProduct(false)}
        onSuccess={() => {
          fetchProductsHandler();
          setShowUploadProduct(false);
        }}
      />
    );
  } else if (showQuotations) {
    bodyContent = (
      <QuotationPanel
        token={token!}
        onClose={() => setShowQuotations(false)}
        requests={quotationRequests}
      />
    );
  } else if (showOrders) {
    bodyContent = <OrderPanel token={token!} onClose={() => setShowOrders(false)} />;
  } else {
    bodyContent = (
      <div className="sellerhome-scrollable">
        <BannerAd />
        <div className="sellerhome-section-header">
          <h2 className="sellerhome-section-heading">Products</h2>
          <div className="sellerhome-buttons">
            <button
              className="sellerhome-upload-button"
              onClick={() => setShowUploadProduct(true)}
            >
               Upload Excel
            </button>
            <button
              className="sellerhome-add-button"
              onClick={() => setShowAddProduct(true)}
            >
               Add Product
            </button>
          </div>
        </div>
        <ProductsList products={products} onSelectProduct={setSelectedProduct} />
      </div>
    );
  }

  // ----------------- Return Layout -----------------
  return (
    <div className="sellerhome-container">
      <div className="sellerhome-header">
        <Header
          onUserPress={() => {}}
          onCartPress={() => {}}
          onNotificationsPress={async () => {
            setShowNotifications((prev) => !prev);
            await markUnreadAsRead();
          }}
          unreadCount={unreadCount}
        />
      </div>

      <div className="sellerhome-body">
        <div className="sellerhome-sidebar">
          <Sidebar
            onDashboardPress={() => {
              setShowDashboard(true);
              setShowChat(false);
              setShowOrders(false);
              setShowQuotations(false);
              setShowAddProduct(false);
              setShowUploadProduct(false);
              setSelectedProduct(null);
              setShowNotifications(false);
            }}
            onChatPress={() => {
              setShowDashboard(false);
              setShowChat(true);
            }}
            onQuotationsPress={() => {
              fetchQuotationRequestsHandler();
              setShowDashboard(false);
              setShowQuotations(true);
            }}
            onOrdersPress={() => {
              setShowDashboard(false);
              setShowOrders(true);
            }}
          />
        </div>

        <div className="sellerhome-content">{bodyContent}</div>
      </div>
    </div>
  );
}
