import React, { useState } from "react";
import "./Categories.css";
import { Product } from "../../types";

type Category = {
  name: string;
  image: string;
};

type Props = {
  token: string;
  products: Product[];
  onSelectCategory: (categoryName: string) => void;
};

const DEFAULT_IMAGE = "https://via.placeholder.com/150";

export default function Categories({ token, products, onSelectCategory }: Props) {
  const [startIndex, setStartIndex] = useState(0);
  const visibleCount = 7;

 
  const categories: Category[] = Array.from(
    products.reduce((map, product) => {
      if (!product.category) return map;

      if (!map.has(product.category)) {
        map.set(product.category, {
          name: product.category,
          image: product.image || DEFAULT_IMAGE,
        });
      }

      return map;
    }, new Map<string, Category>())
  ).map(([, value]) => value);

  //  Carousel navigation
  const handlePrev = () => setStartIndex((prev) => Math.max(prev - 1, 0));
  const handleNext = () =>
    setStartIndex((prev) =>
      Math.min(prev + 1, categories.length - visibleCount)
    );

  const visibleCategories = categories.slice(startIndex, startIndex + visibleCount);

  return (
    <div className="categories-container">
      <div className="categories-title">
        <h3>Categories</h3>
      </div>

      {categories.length === 0 ? (
        <div className="categories-empty">
          <div className="categories-empty-icon"></div>
          <p>No categories found</p>
          <span>Try adding products or updating filters.</span>
        </div>
      ) : (
        <div className="categories-row">
          <button
            className={`nav-btn ${startIndex === 0 ? "disabled" : ""}`}
            onClick={handlePrev}
            disabled={startIndex === 0}
          >
            &#8249;
          </button>

          <div className="cards-container">
            {visibleCategories.map((cat, idx) => (
              <div
                key={idx}
                className="category-card"
                onClick={() => onSelectCategory(cat.name)}
              >
                <img
                  src={cat.image || DEFAULT_IMAGE}
                  alt={cat.name}
                  onError={(e) => {
                    e.currentTarget.src = DEFAULT_IMAGE;
                  }}
                />
                <p>{cat.name}</p>
              </div>
            ))}
          </div>

          <button
            className={`nav-btn ${
              startIndex >= categories.length - visibleCount ? "disabled" : ""
            }`}
            onClick={handleNext}
            disabled={startIndex >= categories.length - visibleCount}
          >
            &#8250;
          </button>
        </div>
      )}
    </div>
  );
}
